import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table } from 'antd';
import { Select } from 'antd';
import { Card } from 'antd';
import {  Button } from 'antd';
import { Pagination } from 'antd';
import { Menu } from 'antd';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from './../../../redux/actions';
import  {requestApi}  from './../../../api/index';
import GestionImpuestoPredial  from './GestionImpuestoPredial';
import { Tabs } from 'antd';
import { Input } from 'antd';
import { Content } from 'antd/lib/layout/layout';
const { Option } = Select;

const { TabPane } = Tabs;
const  PrincipalImpuestoPredial=()=> {

  const columns = [
    {
      title: 'Año',
      dataIndex: 'anioPago',
      sorter: false,
      render: data => `${data}`,
      width: '20%',
      key: 'anioPago-01',
    },
    {
      title: 'pago',
      dataIndex: 'valorPago',
      sorter: false,
      render: data => `${data}`,
      width: '20%',
      key: 'valorPago-01',
    },  
    {
      title: 'Pago Fraccionado',
      dataIndex: 'pagoFraccionado',
      sorter: false,
      render: data => `${data}`,
      width: '20%',
      key: 'pagoFraccionado-01',
    },
    {
      title: 'Fecha Registro',
      dataIndex: 'fechaRegistro',
      sorter: false,
      render: data => `${data}`,
      width: '20%',
      key: 'dni-01',
    },
    {
      title: 'Código Predio',
      dataIndex: 'codigoPredio',
      sorter: false,
      render: data => `${data}`,
      width: '20%',
      key: 'codigoPredio-01',
    }, 
    {
      title: 'direcciòn',
      dataIndex: 'direccion',
      sorter: false,
      width: '20%',
      key: 'nombres-01',
      render: data => `${data}`,
    },
    {
      title: 'Numero Direcciòn',
      dataIndex: 'numeroDireccion',
      sorter: false,
      width: '20%',
      key: 'apellidoPaterno-01',
      render: data => `${data}`
    },
    {
      title: 'Numero Documento',
      dataIndex: 'numeroDocumento',
      sorter: false,
      key: 'numeroDocumento-01',
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Nombre',
      dataIndex: 'nombres',
      sorter: false,
      key: 'nombres-01',
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Apelido Paterno',
      dataIndex: 'apellidoPaterno',
      sorter: false,
      key: 'apellidoPaterno-01',
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Apelido Materno',
      dataIndex: 'apellidoMaterno',
      sorter: false,
      key: 'apellidoMaterno-01',
      render: data => `${data}`,
      width: '20%'
    }
  ];
  const [loading,setLoading] = useState(false);
  //const dispatch = useDispatch();
  const [listadoDataCiudadano,setListadoDataCiudadano] = useState([]);
  const listRef = React.createRef();
  const [contarTotal, setContarTotal] = useState(0);
  const [paginaActual, setPaginaActual] = useState(1);
  const [cantidadContenido, setCantidadContenido] = useState(10);
  //const [data,setData] = useState([]);

  useEffect(()=>{
    let isSubscribed = true
    console.log("aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa");
    if(listRef?.current!=null){
      console.log("aaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaaa");
      setLoading(true);
      listarImpuestoPredial(paginaActual-1,cantidadContenido);
      setLoading(false);
      if (!isSubscribed) return;
    }
    
    return () => isSubscribed = false;
  },[]);

  
  const refrescarListado=async()=>{
    await listarImpuestoPredial(paginaActual-1,cantidadContenido);
  }
  const listarImpuestoPredial = async(reqpaginaActual,reqcantidadContenido) => {
    try{
      let respon=await requestApi(
        `impuestos/listadoImpuestoPredial?paginaActual=${(reqpaginaActual)}&cantidadContenido=${reqcantidadContenido}`,
        {},
        "get"
      );
      setListadoDataCiudadano(respon.estado ? respon.data : []  );
      setContarTotal(respon.estado ? respon.cantidad : 0  );
    }catch(e){
      return e;
    }finally{
     
    }
   
  };

  const changePagination=async(val,sizePagination)=>{
    console.log("val",val);
    setPaginaActual(val);
    setCantidadContenido(sizePagination);
    await listarImpuestoPredial(val-1,sizePagination);
 }
  return (<div ref={listRef}>
    <Tabs  defaultActiveKey="1" onChange={(e)=>{console.log("eeeeuuu",e);
      if(e=="1"){
        refrescarListado(); 
      }
    
    }} >
        <TabPane tab="Listado" key="1">
          <Content>
              <Table
                scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
                columns={columns}
                dataSource={listadoDataCiudadano}
                rowKey={(record) =>(record.id)}
                pagination={false}
                loading={loading}
              />
            
              <Pagination style={{marginTop:50,position: "absolute",right:44}}  className="paginationTable" current={paginaActual} defaultCurrent={paginaActual} total={contarTotal}  onChange={(val,size)=>{changePagination(val,size)}} />
          </Content>
        </TabPane>
        <TabPane tab="Registrar Impuesto Predial" key="impt-2">
          <GestionImpuestoPredial/>
        </TabPane>
    </Tabs>

    </div>
    );
 
}
export default PrincipalImpuestoPredial;