import {useState,useEffect} from 'react';
import  {requestApi}  from '../../api/index';
import swal from 'sweetalert';
import { PageHeader } from 'antd';
import moment from 'moment';
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import { DatePicker } from 'antd';
import { InputNumber } from 'antd';
import {
    Form,
    Input,
    Button,
    Select,
    Switch,Table,Modal
  } from 'antd';
import { Content } from 'antd/lib/layout/layout';
const { Option } = Select;

const  FormularioEditarUsuario=(props)=> {
    const columnsPredio = [
      {
        title: 'Código Dirección',
        dataIndex: 'codigoPredio',
        sorter: false,
        render: data => `${data}`,
        width: '20%',
        key: 'dni-01',
      },
      {
        title: 'Dirección',
        dataIndex: 'direccion',
        sorter: false,
        render: data => `${data}`,
        width: '40%',
        key: 'dni-01',
      },
      {
        title: 'Numero Dirección',
        dataIndex: 'numeroDireccion',
        sorter: false,
        width: '20%',
        key: 'nombres-01',
        render: data => `${data}`,
      },
      {
        title: 'Vigencia',
        dataIndex: 'vigenciaDireccionText',
        sorter: false,
        width: '20%',
        key: 'vigencia',
        render: data => `${data}`
      }
    ];

    const columnsPredioCostosHistorico = [
      {
        title: 'Fecha Registro',
        dataIndex: 'fechaRegistro',
        sorter: false,
        render: data => `${data}`,
        width: '20%',
        key: 'fechaRegistro-01',
      },
      {
        title: 'Costo Predio',
        dataIndex: 'costoPredio',
        sorter: false,
        render: data => `${data}`,
        width: '40%',
        key: 'dni-01',
      },
      {
        title: 'Fecha Inicio',
        dataIndex: 'fechaInicio',
        sorter: false,
        width: '20%',
        key: 'fechaInicio-01',
        render: data => `${data}`,
      },
      {
        title: 'Fecha Fin',
        dataIndex: 'fechaFin',
        sorter: false,
        width: '20%',
        key: 'fechaFin',
        render: data => `${data}`
      }
    ];
    const [form] = Form.useForm();
    const [formPredio] = Form.useForm();
    const [componentSize, setComponentSize] = useState('large');
    const listRef = React.createRef();
    const [vigencia, setVigencia] = React.useState(true);
    const [mensajeErrorDocumento, setMensajeErrorDocumento] = React.useState(null);
    const [mensajeErrorCorreo, setMensajeErrorCorreo] = React.useState(null);

    const [selectionType] = useState('radio');
    const [isModalDirecciones, setIsModalDirecciones] = React.useState(false);
    const [listadoDirecciones, setListadoDirecciones] = React.useState([]);
    const [direccionSeleccionada, setDireccionSeleccionada] = React.useState(null);
    const [vigenciaDireccion, setVigenciaDireccion] = React.useState(true);
    const [loading,setLoading] = useState(false);
    const [loadingCostos,setLoadingCostos] = useState(false);
    const [fechaInicio, setFechaInicio] = useState(null);
    const [fechaFin, setFechaFin] = useState(null);
    const [direccionSeleccionadaTable, setDireccionSeleccionadaTable] = React.useState([]);
    const [listadoDireccionesCostoHistoricos, setListadoDireccionesCostoHistoricos] = React.useState([]);
    const dispatch = useDispatch();
    const onFormLayoutChange = ({ size }) => {
      setComponentSize(size);
    };
  useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        (async()=>{
          setLoading(true);
          console.log("props.dataEdit",props);
          if(props.dataEdit!=null){
            /*form.setFieldsValue({
              correo: props.dataEdit.correo
            });*/
            form.setFieldsValue({
              dni: props.dataEdit.numeroDocumento
            });
            form.setFieldsValue({
              nombres: props.dataEdit.nombres
            });
            form.setFieldsValue({
              apellidoPaterno: props.dataEdit.apellidoPaterno
            });
            form.setFieldsValue({
              apellidoMaterno: props.dataEdit.apellidoMaterno
            });
            form.setFieldsValue({
              fechaNacimiento: moment(props.dataEdit.fechaNacimiento)
            });
            setListadoDirecciones(props.dataEdit.predios);
            setLoading(false);
            //fechaNacimiento
            console.log("props.dataEdit",props.dataEdit);
            setVigencia(props.dataEdit.vigencia);
          //obtenerListadoDirecciones();
        }else{
          form.setFieldsValue({
            correo:""
          });
          form.setFieldsValue({
            dni:""
          });
          form.setFieldsValue({
            nombres:""
          });
          form.setFieldsValue({
            apellidoPaterno:""
          });
          form.setFieldsValue({
            apellidoMaterno:""
          });
          setVigencia(true);
          //form.getFieldValue("genero")
          form.setFieldsValue({
            genero:null
          });
          
        }
      })();

      if (!isSubscribed) return;
    }
    
    return () => isSubscribed = false;
  },[]);

  useEffect(()=>{
    let isSubscribed = true
    if(listRef?.current!=null){
      (async()=>{
        setLoading(true);
        if(direccionSeleccionadaTable.length){
          await obtenerListadoDirecciones(direccionSeleccionadaTable[0].id);
        }else{
          setListadoDireccionesCostoHistoricos([]  );
        }
        setLoading(false);
    })();

    if (!isSubscribed) return;
  }
  
  return () => isSubscribed = false;
},[direccionSeleccionadaTable]);
  //props.dataEdit
  const onBlur=(val)=>{
    if(val.target.value){
      verificarDni(val.target.value);
    }else{
      setMensajeErrorDocumento(null);
    }
  }
  const onBlurCorreo=(val)=>{
    if(val.target.value){
      verificarCorreo(val.target.value);
    }else{
      setMensajeErrorCorreo(null);
    }
  }
  //setListadoDireccionesCostoHistoricos
  const obtenerListadoDirecciones = async(reqId) => {
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `datosUsuarioCiudadano/obtenerCostoPredio?idPredio=`+reqId,
        {},
        "get"
      );
      setListadoDireccionesCostoHistoricos(respon.estado ? respon.data : []  );
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  
  };
  const verificarDni = async(dni) => {
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `usuarioAdmin/verificarDniUsuarioAdmin?dni=`+dni,
        {},
        "get"
      );
      setMensajeErrorDocumento(respon.estado ? null : respon.mensaje  );
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  
  };
  const actualizarCiudadano=async()=>{
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let dataRequest={
        "id":props.dataEdit.id,
        "dni":form.getFieldValue("dni"),
        //"correo":form.getFieldValue("correo"),
        "nombre":form.getFieldValue("nombres"),
        "apellidoPaterno":form.getFieldValue("apellidoPaterno"),
        "apellidoMaterno":form.getFieldValue("apellidoMaterno"),
        "fechaNacimiento":form.getFieldValue("fechaNacimiento"),
        "idGenero":form.getFieldValue("genero"),
        "vigencia":vigencia,
        "arrayPredio":listadoDirecciones
      };
      let respon=await requestApi(
        `datosUsuarioCiudadano/actualizarDatosCiudadano`,
        dataRequest,
        "post"
      );
      if(respon.estado){
        swal({
          title: "Actualizar Ciudadano.",
          text: respon.mensaje,
          icon:  respon.estado ? "success":"error",
          button: "aceptar"
        });
        props.atras();
        props.refrescarListado();
      }else{
        swal({
          title: "Actualizar Ciudadano.",
          text: respon.mensaje,
          icon:  respon.estado ? "success":"error",
          button: "aceptar"
        });
      }
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  
  }
  const verificarCorreo = async(correo) => {
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `usuarioAdmin/verificarCorreoUsuarioAdmin?correo=`+correo,
        {},
        "get"
      );
      setMensajeErrorCorreo(respon.estado ? null : respon.mensaje  );
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  
  };
  const handleDirecciones= ()=> {
    formPredio.validateFields()
    .then(async(values) => {
      let tempListadoDirecciones=JSON.parse(JSON.stringify(listadoDirecciones));
      if(direccionSeleccionada==null){
        tempListadoDirecciones.push({
          key:listadoDirecciones.length,
          codigoPredio:formPredio.getFieldValue("codigoPredio"),
          direccion:formPredio.getFieldValue("direccion"),
          numeroDireccion:formPredio.getFieldValue("numeroDireccion"),
          vigenciaDireccion:vigenciaDireccion,
          vigenciaDireccionText:vigenciaDireccion ? "ACTIVO" : "INACTIVO"
        } );
      }else{
        tempListadoDirecciones[direccionSeleccionada]={
          id:tempListadoDirecciones[direccionSeleccionada].id,
          key:direccionSeleccionada,
          direccion:formPredio.getFieldValue("direccion"),
          codigoPredio:formPredio.getFieldValue("codigoPredio"),
          numeroDireccion:formPredio.getFieldValue("numeroDireccion"),
          vigenciaDireccion:vigenciaDireccion,
          vigenciaDireccionText:vigenciaDireccion ? "ACTIVO" : "INACTIVO"
        };
        
      }
     
      setListadoDirecciones(JSON.parse(JSON.stringify(tempListadoDirecciones)));
      setIsModalDirecciones(false);
      //console.log("tempListadoDirecciones-tempListadoDirecciones",JSON.stringify(tempListadoDirecciones));
    })
    .catch((errorInfo) => {
      //console.log("handleDirecciones"+errorInfo);
    });
  }

  

  const generarCostoPredio=()=>{
    setIsModalDirecciones(true);
  }
  const handleCancelDirecciones= ()=> {
    setIsModalDirecciones(false);
    setDireccionSeleccionada(null);
  }

  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      setDireccionSeleccionadaTable(selectedRows);
      console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
    },
    getCheckboxProps: (record) => ({
      disabled: record.name === 'Disabled User', // Column configuration not to be checked
      name: record.name,
    }),
  };
  return (
    <div ref={listRef}   className="contentPago">
      <Modal width={"50%"} 
      centered 
      title= {"Costo Predio"} 
      visible={isModalDirecciones} 
      onOk={handleDirecciones} 
      onCancel={handleCancelDirecciones}>
      <Form
          labelCol={{
          span: 24,
          }}
          wrapperCol={{
          span: 24,
          }}
          layout="vertical"
          initialValues={{
          size: componentSize,
          }}
          size={componentSize}
          form={formPredio}
          className="formPredio"
          >
              <Form.Item 
                name={['fechaInicio']}
                label="Fecha Inicio"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Inicio.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                
              >
                  <DatePicker  
                    name={[ 'fechaInicioDataPinker']}                       
                    allowClear
                    style={{width:"100%"}}
                    label=""  
                    placeholder="" 
                    defaultValue={null} 
                    value={fechaInicio==null ? null :  moment(fechaInicio, 'YYYY-MM-DD')}   
                    onChange={(value)=>{value._d!=undefined ? setFechaInicio(value._d) : setFechaInicio(null) } }
                    disabledDate={(current) => {
                      let customDate = moment().format("YYYY-MM-DD");
                      return current && current > moment(customDate, "YYYY-MM-DD");
                    }} />
              </Form.Item>
              <Form.Item 
                name={['valorPredio']}
                label="Valor Predio"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Valor Predio.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                
              >
                  <InputNumber
                  className="inputrowremove" 
                  autoComplete="off"
                  type="number" 
                  
                />
              </Form.Item>
              <Form.Item 
                name={['fechaFin']}
                label="Fecha Fin"
                max="500"
                rules={[{ required: false, message: 'Se Requiere de Fecha Fin.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                
              >
                  <DatePicker  
                    name={[ 'fechaFinDataPinker']}                       
                    allowClear
                    style={{width:"100%"}}
                    label=""  
                    placeholder="" 
                    defaultValue={null} 
                    value={fechaFin==null ? null :  moment(fechaFin, 'YYYY-MM-DD')}   
                    onChange={(value)=>{value._d!=undefined ? setFechaFin(value._d) : setFechaFin(null) } }
                    disabledDate={(current) => {
                      let customDate = moment().format("YYYY-MM-DD");
                      return current && current > moment(customDate, "YYYY-MM-DD");
                    }} />
              </Form.Item>
          </Form>
      </Modal> 
      <Card title={<PageHeader
        className="site-page-header"
        onBack={() => props.atras()}
        title="Asignar Costo Predio Ciudadano"
        subTitle={null}
      />} bordered={false} style={{ width: "100%" }}>
        <>
        <Form
            labelCol={{
            span: 24,
            }}
            wrapperCol={{
            span: 24,
            }}
            layout="vertical"
            initialValues={{
            size: componentSize,
            }}
            onValuesChange={onFormLayoutChange}
            size={componentSize}
            form={form}
        >
            <Form.Item 
              name={['dni']}
              label="dni"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Dni.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              
            >
                <Input
                
                disabled
                className="inputrowremove" 
                type="number" onInput = {(e) =>{
                setMensajeErrorDocumento(null);
                if(!(e.target.value=="" || e.target.value==null) ){  
                  e.target.value = Math.max(0, parseInt(e.target.value) ).toString().slice(0,8)
                }
                
                }}
                onBlur={(e)=>{onBlur(e);}}
              />
              
            </Form.Item>
             {
                  mensajeErrorDocumento==null ? null :
                <span className="errorMensaje">{mensajeErrorDocumento}</span>
              }
            <Form.Item 
              name={['correo']}
              label="Correo"
              max="500"
              style={{display:"none"} }
              rules={[{ required: false, message: 'Se Requiere de Correo.' },{
                type: "email",
                message: "El formato del correo es invalido."
              }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              
            >
                <Input   
                 disabled
                 onInput = {(e) =>{
                    setMensajeErrorCorreo(null);
                  
                }}
                onBlur={(e)=>{onBlurCorreo(e);}} />
            </Form.Item>
            {
                  mensajeErrorCorreo==null ? null :
                <span className="errorMensaje">{mensajeErrorCorreo}</span>
              }
            <Form.Item 
              name={[ 'nombres']}
              label="Nombres"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Nombres.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              >
                <Input disabled autoComplete="off" />
            </Form.Item>
            <Form.Item 
              name={['apellidoPaterno']}
              label="Apellido Paterno"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Apellido Paterno.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}>
                <Input disabled />
            </Form.Item>
            <Form.Item 
              name={[ 'apellidoMaterno']}
              label="Apellido Materno"
              max="500"
              rules={[{ required: true, message: 'Se Requiere de Apellido Materno.' }]}
              wrapperCol={{ lg: 14,md:14,sm: 24 }}
              >
                <Input disabled autoComplete="off" />
            </Form.Item>
            <Table
                 rowSelection={{
                  type: selectionType,
                  ...rowSelection,
                }}
                style={{marginBottom:87}}
                className="contentTable"
                scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
                columns={columnsPredio}
                dataSource={listadoDirecciones}
                rowKey={(record) =>(record.key)}
                pagination={false}
                loading={loading}
              />
            <Content>
              {
                direccionSeleccionadaTable.length ?
                <Button type="primary" onClick={()=>{generarCostoPredio()}} >GenerarCosto Predio </Button >
                :
                null
              }
              <span>{JSON.stringify(listadoDireccionesCostoHistoricos)}</span>
              <Table
              style={{marginTop:20}}
              scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
              columns={columnsPredioCostosHistorico}
              dataSource={listadoDireccionesCostoHistoricos}
              rowKey={(record) =>(record.fechaRegistro)}
              pagination={false}
              loading={loadingCostos}
              />
            </Content>
           
            <Button style={{marginTop:101}}  htmlType="submit" className="btnNuevoRegistrar" type="primary" onClick={()=>{ actualizarCiudadano()}}>Actualizar Usuario</Button>
        </Form>
        </>
      </Card>
    </div>
    );
 
}
export default FormularioEditarUsuario;