import {useState,useEffect} from 'react';
import { Layout } from 'antd';
import  {requestApi}  from '../../../api/index';
import { List } from 'antd';
import moment from 'moment';
import {  useDispatch } from "react-redux";
import { Button,Table } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {loaderSpiner} from '../../../redux/actions';
import GestionImpuestoCiudadanoComponent from './GestionImpuestoCiudadano';
import VerGestionImpuestoComponent from './VerGestionImpuesto';
import { Pagination } from 'antd';
import { Drawer } from 'antd';
import { Input } from 'antd';
import { DatePicker, Space } from 'antd';
import { Select, Radio } from 'antd';
import { Tag } from 'antd';
const {  Content } = Layout;
const { Option } = Select;

const { RangePicker } = DatePicker;
const  ListadoGestionImpuestoCiudadano=(props)=> {
    
    const children = [];
    for (let i = 10; i < 36; i++) {
      children.push(<Option key={i.toString(36) + i}>{i.toString(36) + i}</Option>);
    }
    const columns = [
      { title: 'fecha Registro', dataIndex: 'fechaRegistro', key: 'fechaRegistro',center:true },
      { title: 'dirección', dataIndex: 'direccion', key: 'direccion',center:true },
      { title: 'Numero Dirección', dataIndex: 'numeroDireccion', key: 'direccionnumeroDireccion',center:true },
      { title: 'Numero Documento', dataIndex: 'numeroDocumento', key: 'numeroDocumento',center:true },
      { title: 'nombres', dataIndex: 'nombres', key: 'nombres' },
      { title: 'Apellido Paterno', dataIndex: 'apellidoPaterno', key: 'apellidoPaterno' },
      { title: 'Apellido Materno', dataIndex: 'apellidoMaterno', key: 'apellidoMaterno' },
      { title: 'Apellido Materno', dataIndex: 'apellidoMaterno', key: 'apellidoMaterno' },
      { title: 'sub Total', dataIndex: 'subTotal', key: 'subTotal' }
    ];
      const [gestionarNuevo, setGestionarNuevo] = useState(1);
      const [anioSeleccionado/*, setAnioSeleccionado*/] = useState(null);
      const [listadoImpuestoAnuales, setListadoImpuestoAnuales] = useState([]);
      const [paginaActual, setPaginaActual] = useState(1);
      const [cantidadContenido, setCantidadContenido] = useState(10);
      const [numeroDocumentoFiltro, setNumeroDocumentoFiltro] = useState("");
      const [tipoDocumentoFiltro, setTipoDocumentoFiltro] = useState(null);
      const [tipoDocumentoFiltroDefault, setTipoDocumentoFiltroDefault] = useState(null);
      const [listTipoDocumento, setListTipoDocumento] = useState([]);
      /*
      setTipoDocumentoFiltro(e)
      */
      const [predioSeleccionadoFiltro, setPredioSeleccionadoFiltro] = useState();
      const [fechaRegistroFiltro, setFechaRegistroFiltro] = useState(null);
      const [fechaRegistroRangoFiltro, setFechaRegistroRangoFiltro] = useState(null);
      const [listadoPrediosClientes, setListadoPrediosClientes] = useState([]);

      //listadoPredioSeleccionados
      const [listFiltro, setFiltro] = useState({
        tipoDocumentoFiltro:null,
        numeroDocumentoFiltro:null,
        fechaRegistroFiltro:null,
        predioSeleccionadoFiltro:null
      });
      /*
       objTipoDocumento["tipoDocumentoFiltro"]={
          valor:tipoDocumentoFiltro,
          texto:objItemTipoDocument.length ? objItemTipoDocument[0].descripcion : null,
          etiqueta:"Tipo Documento"
        };
        objTipoDocumento["numeroDocumentoFiltro"]={
          valor:numeroDocumentoFiltro,
          texto:numeroDocumentoFiltro,
          etiqueta:"Numero Documento"
        };
        objTipoDocumento["fechaRegistroFiltro"]={
          valor:fechaRegistroFiltro,
          texto:fechaRegistroFiltro,
          etiqueta:"Fecha Pago"
        };
        let objItemPredio=listadoPrediosClientes.filter((val)=>{
          return val.id==predioSeleccionadoFiltro;
        });
        objTipoDocumento["predioSeleccionadoFiltro"]={
          valor:predioSeleccionadoFiltro==undefined ? null :predioSeleccionadoFiltro ,
          texto:objItemPredio.length ? objItemPredio[0].direccion : null,
          etiqueta:"Predio"
        };
      */
      const [contarTotal, setContarTotal] = useState(0);
      //const [contarTotal, setContarTotal] = useState(0);
      const [tipoBusqueda/*, setTipoBusqueda*/] = useState("completeNames");
      const [inputSearch/*, setInputSearch*/] = useState("");
      const [visible, setVisible] = useState(false);
      const listRef = React.createRef();
      const dispatch = useDispatch();
     useEffect(()=>{
      let isSubscribed = true;
      if(listRef?.current!=null){
        cargarTipoDocumento();
        listarImpuestoAnuales(paginaActual-1,cantidadContenido);
        return () => isSubscribed = false;
       }
      
      return () => isSubscribed = false;
      },[]);
      const listarImpuestoAnuales = async(reqpaginaActual,reqcantidadContenido) => {
          dispatch(loaderSpiner({
            loaderSpiner:true
          }));
          try{
            let respon=await requestApi(
              `impuestos/listadoImpuestosCiudadanos?paginaActual=${reqpaginaActual}&cantidadContenido=${reqcantidadContenido}&tipoBusqueda=${tipoBusqueda}&valorBusqueda=${inputSearch}`,
              {},
              "get"
            );
            dispatch(loaderSpiner({
              loaderSpiner:false
            }));
            setListadoImpuestoAnuales(respon.estado ? respon.data : []);
            setContarTotal(respon.estado ? respon.cantidadContenido : 1);
          }catch(e){
            return [];
          }finally{
            dispatch(loaderSpiner({
              loaderSpiner:false
            }));      
          }
        
      };
      const cargarTipoDocumento = async() => {
        dispatch(loaderSpiner({
          loaderSpiner:true
        }));
        try{
          let respon=await requestApi(
            `dataGeneral/cargarTipoDocumento`,
            {},
            "get"
          );
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));
          console.log("respon.defaultDocument",respon.defaultDocument);
          setListTipoDocumento(respon.estado ? respon.data : []);
          setTipoDocumentoFiltro(respon.estado ? (""+respon.defaultDocument) : null);
          setTipoDocumentoFiltroDefault(respon.estado ? (""+respon.defaultDocument) : null);
          //defaultDocument
          //tipoDocumentoFiltroDefault, setTipoDocumentoFiltroDefault;

        }catch(e){
          return [];
        }finally{
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));      
        }
      
      };
      const cargarPrediosCiudadanos = async() => {
        dispatch(loaderSpiner({
          loaderSpiner:true
        }));
        try{
          let respon=await requestApi(
            `datosUsuarioCiudadano/buscarPredioCiudadanoTipoDocumento?dni=${numeroDocumentoFiltro}&tipoDocumento=${tipoDocumentoFiltro}`,
            {},
            "get"
          );
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));
          setListadoPrediosClientes(respon.estado ? respon.data : []);
          setContarTotal(respon.estado ? respon.cantidadContenido : 1);
        }catch(e){
          return [];
        }finally{
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));      
        }
      
      };
      const changePagination=async(val,sizePagination)=>{
        setPaginaActual(val);
        setCantidadContenido(sizePagination);
        
        await listarImpuestoAnuales(val-1,sizePagination);
      }
      const nuevoImpuestos = () => {
        setGestionarNuevo(2);
      };
      const listFiltrarDatos=()=>{
        let objTipoDocumento={};
        let objItemTipoDocument=listTipoDocumento.filter((val)=>{
          return val.id==tipoDocumentoFiltro;
        });
        //
        objTipoDocumento["numeroDocumentoFiltro"]={
          valor:!(numeroDocumentoFiltro=="" || numeroDocumentoFiltro==null) ? numeroDocumentoFiltro : null,
          texto:!(numeroDocumentoFiltro=="" || numeroDocumentoFiltro==null) ? numeroDocumentoFiltro : null,
          etiqueta:objItemTipoDocument.length ? objItemTipoDocument[0].descripcion : null
        };
        if((numeroDocumentoFiltro=="" || numeroDocumentoFiltro==null)){
          objTipoDocumento["numeroDocumentoFiltro"]=null;
        }
        //fechaRegistroFiltro
        if(fechaRegistroFiltro==null || fechaRegistroFiltro==""){
          objTipoDocumento["fechaRegistroFiltro"]=null;
        }else{
          objTipoDocumento["fechaRegistroFiltro"]={
            valor: fechaRegistroFiltro,
            texto: (moment(fechaRegistroFiltro[0]).format("YYYY-MM-DD")+":"+moment(fechaRegistroFiltro[1]).format("YYYY-MM-DD")),
            etiqueta:"Fecha Pago"
          };
        }
        
        
        let tempValores=(predioSeleccionadoFiltro=="" || predioSeleccionadoFiltro==null  || predioSeleccionadoFiltro==undefined) ? [] :predioSeleccionadoFiltro ;
       
        let labelPredio=[];
        tempValores.map((val)=>{
          let existe=listadoPrediosClientes.filter((valPrediosCli)=>{
            return valPrediosCli.id==val;
          });
          if(existe.length){
            labelPredio.push(existe[0].direccion);
          }
        });
        if(labelPredio.length){

          objTipoDocumento["predioSeleccionadoFiltro"]={
            valor:predioSeleccionadoFiltro ,
            texto:labelPredio.length ? labelPredio.join(",") : null,
            etiqueta:"Predio"
          };
        }else{
          objTipoDocumento["predioSeleccionadoFiltro"]=null;
        }
        setFiltro(JSON.parse(JSON.stringify(objTipoDocumento)));
        setVisible(!visible);
        
      }
      const cambiarFechaPago=(value)=>{
        if( value==null){
          setFechaRegistroFiltro(null);
          return false;
        }
        if(value!=undefined ){
          setFechaRegistroFiltro(value);
        }else{
          setFechaRegistroFiltro(null);
        }
      }
      const buscarCiudadanoPredioTipoDocumento=()=>{
        if(numeroDocumentoFiltro=="" || numeroDocumentoFiltro==null){
          setListadoPrediosClientes([]);
          return false;
        }
        setPredioSeleccionadoFiltro(undefined);
        cargarPrediosCiudadanos();
      }
      const filtrarDatosModal=async()=>{
        //setVisible(!visible)
        setVisible(!visible);
        if(listFiltro["tipoDocumentoFiltro"]==null){
          setTipoDocumentoFiltro(tipoDocumentoFiltroDefault);
        }
        if(listFiltro["numeroDocumentoFiltro"]==null){
          setNumeroDocumentoFiltro(undefined);
        }else{
          
          setNumeroDocumentoFiltro(listFiltro["numeroDocumentoFiltro"]["valor"]);
          await cargarPrediosCiudadanos();
        }
        if(listFiltro["predioSeleccionadoFiltro"]==null){
          setPredioSeleccionadoFiltro(undefined);
        }else{
          setPredioSeleccionadoFiltro(listFiltro["predioSeleccionadoFiltro"]["valor"]);
        }
        if(listFiltro["fechaRegistroFiltro"]==null){
          setFechaRegistroFiltro(undefined);
        }else{
          setFechaRegistroFiltro(listFiltro["fechaRegistroFiltro"]["valor"]);
        }
      }
      const cancelarFiltrarDatosModal=()=>{
        //setVisible(!visible)
        setVisible(!visible);
        if(listFiltro["tipoDocumentoFiltro"]==null){
          setTipoDocumentoFiltro(tipoDocumentoFiltroDefault);
        }
        if(listFiltro["numeroDocumentoFiltro"]==null){
          setNumeroDocumentoFiltro(undefined);
        }else{
          setNumeroDocumentoFiltro(listFiltro["numeroDocumentoFiltro"]["valor"]);
        }
        if(listFiltro["predioSeleccionadoFiltro"]==null){
          setPredioSeleccionadoFiltro(undefined);
        }else{
          setPredioSeleccionadoFiltro(listFiltro["predioSeleccionadoFiltro"]["valor"]);
        }
        if(listFiltro["fechaRegistroFiltro"]==null){
          setFechaRegistroFiltro(undefined);
        }else{
          setFechaRegistroFiltro(listFiltro["fechaRegistroFiltro"]["valor"]);
        }
      }
      return (
        <div ref={listRef}   className="contentPago">
          <Drawer title="Filtrar" 
          footer={<div style={{display:"flex"}}>
            <Button style= {{width:"100%"}} type="primary" onClick={()=>{listFiltrarDatos()}}>filtrar</Button>
            <Button style= {{width:"100%"}} onClick={()=>{cancelarFiltrarDatosModal()}} >cerrar</Button></div>} 
              destroyOnClose={false}   maskClosable={false} closable={true} placement="right" width={300} onClose={()=>{setVisible(!visible)}} visible={visible}>
            <div>
              <label style={{display:"block"}}>tipo Documento</label>
              <Select
                mode="simple"
                size={100}
                placeholder="seleccione tipo documento"
                value={tipoDocumentoFiltro}
                onChange={(e)=>{buscarCiudadanoPredioTipoDocumento();setTipoDocumentoFiltro(e)}}
                style={{ width: '100%' }}
              >
                {listTipoDocumento.map(item => (
                      <Option id={item.id} key={item.id}>{item.descripcion}</Option>
                ))}
              </Select>
            </div>
            <div>
              <label>Numero  Documento</label>
              <Input value={numeroDocumentoFiltro} placeholder="" onBlur={(e)=>{buscarCiudadanoPredioTipoDocumento()}} onChange={(e)=>{setNumeroDocumentoFiltro(e.target.value)}} />
            </div>
            <div>
              <label style={{display:"block"}}>Fecha Pago</label>

              <RangePicker 
               value={fechaRegistroFiltro==null ? [] : [moment(fechaRegistroFiltro[0], 'YYYY-MM-DD'), moment(fechaRegistroFiltro[1], 'YYYY-MM-DD')]}
      
              onChange={(value)=>{cambiarFechaPago(value) } } />
             
            </div>
            <div>
              <label style={{display:"block"}}>Predio</label>
              <Select
                mode="multiple"
                size={100}
                allowClear
                placeholder="seleccione Predio"
                value={predioSeleccionadoFiltro}
                onChange={(e)=>{setPredioSeleccionadoFiltro(e)}}
                style={{ width: '100%' }}
              >
                {listadoPrediosClientes.map(item => (
                      <Option id={item.id} key={item.id}>{item.direccion}</Option>
                ))}
              </Select>
            </div>
          </Drawer>
          {
            gestionarNuevo==1 ?
              <Card title={"Gestionar Impuesto Ciudano"} bordered={false} style={{ width: "100%" }}>
                <Button type="primary" onClick={nuevoImpuestos}>
                  Agregar o Actualizar
                </Button><br/>
                <Content>
                  <Button className="btnFiltrarList" type="primary" onClick={()=>{filtrarDatosModal();}}>
                    Filtrar 
                  </Button><br/>
                  <div className="listTag">
                      {
                        listFiltro["numeroDocumentoFiltro"]==null ? null : 
                        <Tag
                        closable
                        onClose={() =>{
                          listFiltro["numeroDocumentoFiltro"]=null;
                          setFiltro(JSON.parse(JSON.stringify(listFiltro)) );
 
                       } }
                        >
                        {listFiltro["numeroDocumentoFiltro"]["etiqueta"]+":"+listFiltro["numeroDocumentoFiltro"]["valor"]}
                        </Tag>
                      }
                      {
                        listFiltro["fechaRegistroFiltro"]==null ? null : 
                        <Tag
                        closable
                        onClose={() =>{
                          listFiltro["fechaRegistroFiltro"]=null;
                          setFiltro(JSON.parse(JSON.stringify(listFiltro)) );
 
                       } }
                        >
                        {listFiltro["fechaRegistroFiltro"]["etiqueta"]+":"+listFiltro["fechaRegistroFiltro"]["texto"]}
                        </Tag>
                      }
                      {
                        listFiltro["predioSeleccionadoFiltro"]==null ? null : 
                        <Tag
                        closable
                        visible={listFiltro["predioSeleccionadoFiltro"]!=null}
                        onClose={() =>{
                           listFiltro["predioSeleccionadoFiltro"]=null;
                           setFiltro(JSON.parse(JSON.stringify(listFiltro)) );
  
                        } }
                        >
                        {listFiltro["predioSeleccionadoFiltro"]["etiqueta"]+":"+listFiltro["predioSeleccionadoFiltro"]["texto"]}
                        </Tag>
                      }
                  </div>
                  <div className="contentTableData">
                    <Table
                      scroll={{ x: 'calc(700px + 50%)', y: 340 }}
                      columns={columns}
                      rowKey={record => record.key}
                      dataSource={listadoImpuestoAnuales}
                      pagination={false}
                    />
                    <Pagination  className="paginationTable paginationGestionUsuario" current={paginaActual} defaultCurrent={paginaActual} total={contarTotal}  onChange={(val,size)=>{changePagination(val,size)}} />
                  </div>
                </Content>
                </Card> : null
          }
          {
            gestionarNuevo==2 ?
            <GestionImpuestoCiudadanoComponent atras={()=>{setGestionarNuevo(1);listarImpuestoAnuales();}}/>: null
          }
          {
            gestionarNuevo==3 ?
            <VerGestionImpuestoComponent anioSeleccionado={anioSeleccionado} atras={()=>{setGestionarNuevo(1);listarImpuestoAnuales();}}/>: null
          }
        </div>
        );
 
}
export default ListadoGestionImpuestoCiudadano;