import {useState,useEffect} from 'react';
import { PageHeader } from 'antd';
import { Layout } from 'antd';
import { Row, Col } from 'antd';
import moment from 'moment';
import  {requestApi}  from '../../../api/index';
import swal from 'sweetalert';
import {  useDispatch } from "react-redux";
import { Checkbox } from 'antd';
import { Table } from 'antd';
import { Form, Input, Button, Select } from 'antd';


import { Card } from 'antd';
import * as React from 'react';
import {loaderSpiner} from '../../../redux/actions';
//import { Transfer } from 'antd';
const {  Content } = Layout;
const  GestionImpuestoCiudadano=(props)=> {
    const { Option } = Select;
    const layout = {
      labelCol: {
        span: 8,
      },
      wrapperCol: {
        span: 16,
      },
    };
   
    
    const formRef = React.createRef();
    const [columnasDefiniciones, setColumnasDefiniciones] = useState([]);
    const [pagarTodoPredio, setPagarPredio] = useState(true);
    const [ciudadanoBuscado, setCiudadanoBuscado] = useState(null);
    const [listadoPredio, setListadoPredio] = useState([]);
    const [form] = Form.useForm();
    //onst [selectedRowKeys, setSelectedRowKeys] = useState([]);
    const [listadoDeudaImpuesto, setListadoDeudaImpuesto] = useState([]);
    const [opcionesSeleccionada, setOpcionesSeleccionada] = useState([]);
    const listRef = React.createRef();
    const dispatch = useDispatch();
    useEffect(()=>{
      let isSubscribed = true;
      if(listRef?.current!=null){
        cargarDefiniciones();
        //if (!isSubscribed) return;
        return () => isSubscribed = false;
      }
      
      return () => isSubscribed = false;
    },[]);
    useEffect(()=>{
        let isSubscribed = true;
        return () => isSubscribed = false;
    },[]);
    const cargarDefiniciones = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `impuestos/cargarImpuestos`,
          {},
          "get"
        );
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
        let datosTemp=respon.estado ? respon.data : [];
        let datosTempFormato=[];
        datosTempFormato.push({ title: 'FECHA', dataIndex: 'fechaText', key: 'fechaText' });
        datosTempFormato.push({ title: 'DIRECCIÓN', dataIndex: 'direccionFull', key: 'direccionFull' });
        datosTemp.forEach(element => {
          datosTempFormato.push({ title: element.descripcion , dataIndex: 'column'+element.descripcion, key: 'columnKey'+element.descripcion  });
        });
        datosTempFormato.push({ title: 'SUB TOTAL', dataIndex: 'subTotalText', key: 'subTotalText' });

        setColumnasDefiniciones(datosTempFormato);
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }
    
    };
    const registrarImpuesto=async()=>{
      let valoresSeleccionados=await obtenerValores();
      console.log("hola",valoresSeleccionados);
      
      if(valoresSeleccionados.length==0){
        swal({
          title: "Pago",
          text: "Se requiere Al menos de un Opcción  Seleccionada.",
          icon: "warning",
          button: "aceptar"
        });
      }
      swal({
        title: "Gestion de Pagos.",
        text: " ¿Deseas Guardar Pago Cambios?",
        icon: "warning",
       // buttons: true,
        dangerMode: true,
        buttons: {
          cancel: "Cancelar",
          catch: {
            text: "Aceptar",
            value: "catch",
          }
        }
      })
      .then(async(willDelete) => {
        if (willDelete) {
          dispatch(loaderSpiner({
            loaderSpiner:true
          }));
          try{
            let dataRequest={
              "periodosImpuesto":valoresSeleccionados,
            };
            let respon=await requestApi(
              `impuestos/registrarPago`,
              dataRequest,
              "post"
            );
            if(respon.estado){
              swal({
                title: "Registrar Pago.",
                text: respon.mensaje,
                icon:  respon.estado ? "success":"error",
                button: "aceptar"
              });
              props.atras();
            }else{
              
              swal({
                title: "Registrar Impuesto.",
                text: respon.mensaje,
                icon:  respon.estado ? "success":"error",
                button: "aceptar"
              });
            }
            realizarCalculo();
          }catch(e){
            return e;
          }finally{
            dispatch(loaderSpiner({
              loaderSpiner:false
            }));
          }
        }
      });
      
    }
    const verificarDni = async(dni) => {
      if(dni.target.value=="" || dni.target.value==null || dni.target.value==undefined){
        //nombreCompletos
        setPagarPredio(false);
        setListadoDeudaImpuesto([]);
        form.setFieldsValue({
          predio:undefined
        });
        setCiudadanoBuscado(null);
        form.setFieldsValue({
          nombreCompletos:null
        });
       
        //
        return false;
      }
      form.setFieldsValue({
        predioSeleccionado:undefined
      });
      setListadoDeudaImpuesto([]);
      //setListadoDeudaImpuesto
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `datosUsuarioCiudadano/buscarCiudadano?dni=`+dni.target.value,
          {},
          "get"
        );
        setCiudadanoBuscado(respon.estado ? respon.data : null    );
        if(respon.estado){ //predio
         
          form.setFieldsValue({
            nombreCompletos:respon.data.nombres+" "+respon.data.apellidoPaterno+" "+respon.data.apellidoMaterno
          });
          setListadoPredio(respon.estado ? respon.dataCiudadanosPredio : []    );
        }else{
          form.setFieldsValue({
            nombreCompletos:null
          });
          setListadoPredio( []    );
        }
        //nombreCompletos

      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
     
    };
    const realizarCalculo=async()=>{
      if(ciudadanoBuscado==null){
        swal({
          title: "Impuestos.",
          text:"No seleccion Ciudadano.",
          icon:"waring",
          button: "aceptar"
        });
      }
      let valoresSeleccionadoPredio=form.getFieldValue("predioSeleccionado")=="" || form.getFieldValue("predioSeleccionado")==undefined ? [] : form.getFieldValue("predioSeleccionado");
      let valoresPredioText=valoresSeleccionadoPredio.join(",");
      let valorCiudadanoBuscado=ciudadanoBuscado.id;
      try {
        dispatch(loaderSpiner({
          loaderSpiner:true
        }));
        try{
          let respon=await requestApi(
            `impuestos/obtenerImpuesto?idCliente=${valorCiudadanoBuscado}&arrayDirecciones=${valoresPredioText}`,
            {},
            "get"
          );
          if(respon.estado){
            setListadoDeudaImpuesto(respon.data);
            //dataSeleccinada
            setOpcionesSeleccionada(respon.dataSeleccinada);
          }else{
            setListadoDeudaImpuesto([]);
            setOpcionesSeleccionada([]);
          }
        }catch(e){
          console.log("e",e);
          return e;
        }finally{
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));
        }
       
      } catch (error) {
        
      }
    }
    const obtenerValores=async()=>{
      let valoresFiltrados=[];
      listadoDeudaImpuesto.map(async(val)=>
      {
        //
        /**record.idPredio+"-"+record.idPeriodo */
        let validoSeleccion=opcionesSeleccionada.filter((valFilter)=>{ return valFilter==(val.idPredio+"-"+val.idPeriodo)});
        //return  ? val : val;
        if(validoSeleccion.length){
          valoresFiltrados.push(val);
        }
     });
     return valoresFiltrados;
    }
    
    const rowSelection = {
      selectedRowKeys:opcionesSeleccionada,
      onChange: async(valoresSelec, selectedRows) => {
        
        setOpcionesSeleccionada(valoresSelec);
      },
      getCheckboxProps: (record) => ({
        disabled: record.realizoPagoDefault === true,
        // Column configuration not to be checked
        name: record.name,
      }),
      selections: [
        Table.SELECTION_ALL,
        Table.SELECTION_INVERT,
        Table.SELECTION_NONE,
        {
          key: 'odd',
          text: 'Select Odd Row',
          onSelect: changableRowKeys => {
            console.log("changableRowKeys",changableRowKeys);
            
          },
        }
      ]
    };
    return (
      <div ref={listRef}   className="contentPago">
        <Card title={<PageHeader
        className="site-page-header-left"
        onBack={() =>{props.atras()}}
        title="Gestionar Impuesto Ciudadano"
        subTitle={null}
      />} style={{ width: "100%",float:"left" }}  bordered={false} >
         <Form form={form} {...layout} ref={formRef} name="control-ref"  layout="vertical">
          <Row >
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 3 }}  >
              <Form.Item
                wrapperCol={{lg:24}}
                labelCol={{lg:24}}
                name="ciudadano"
                label="Dni Ciudadano"
                
                rules={[
                  {
                    required: false,
                  },
                ]}
              >
                <Input  autoComplete="off" onBlur={(e)=>{verificarDni(e);}} />
              </Form.Item>
            </Col>
            {
              ciudadanoBuscado==null ? 
              null :
              <>
                <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }}  >
                <Form.Item
                  wrapperCol={{lg:24}}
                  labelCol={{lg:24}}
                  name="nombreCompletos"
                  label="nombres Completos"
                  rules={[
                    {
                      required: true,
                    },
                  ]}
                >
                  <Input  disabled />
                </Form.Item>
              </Col><br/>
              </>
            }
            {
              ciudadanoBuscado==null ? 
              null :
              <>
               <Col  xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }}  >
                  <Checkbox
                    wrapperCol={{lg:24}}
                    labelCol={{lg:24}}
                    className="checkpagarTodos2"
                    checked={pagarTodoPredio}
                    onChange={(e)=>{ if(pagarTodoPredio){form.setFieldsValue({
                      predio:undefined
                    });};setPagarPredio(!pagarTodoPredio); }}
                  >
                  {'Pagar Todos'}
                  </Checkbox>
              </Col>
              </>
            }
            
            {
              !pagarTodoPredio ?
            <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }}   >
            <Form.Item
              wrapperCol={{lg:"24"}}
              name="predioSeleccionado"
              label="Predios"
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Select
                showSearch
                placeholder="Seleccione Predio"
                allowClear
                mode="tags"
              >
                {listadoPredio.map(item => (
                  <Option id={item.id} key={item.id}>{item.codigoPredio+"-"+item.direccion+"-"+item.numero}</Option>
                ))}
               
              </Select>
            </Form.Item>
            </Col>: null

            }
            {
              ciudadanoBuscado==null ? 
                null :
                <Col xs={{ span: 24 }} sm={{ span: 24 }} md={{ span: 24 }} lg={{ span: 12 }}   >
                  <Button type="primary" onClick={()=>{realizarCalculo();}}>
                    Realizar Calculo
                  </Button>
                </Col>
            }
            
            
          </Row>
        </Form>
        <Content>
            <Table
              scroll={{ x: 1000, y: '100%' }} 
              style={{ width: '100%', height: '100%' }} 
              defaultFilteredValue={opcionesSeleccionada}
              rowSelection={{
                type: 'checkbox',
                ...rowSelection,
               }}
               rowKey={record => (record.idPredio+"-"+record.idPeriodo)}
               columns={columnasDefiniciones} 
               dataSource={listadoDeudaImpuesto}
            />

        </Content>
        <Button type="primary" onClick={registrarImpuesto}>
          Registrar
        </Button>
        </Card>
      </div>
      );
 
}
export default GestionImpuestoCiudadano;