import {useState,useEffect} from 'react';
import  {requestApi}  from '../../../api/index';
import { Layout ,Table} from 'antd';
import { Row, Col } from 'antd';
import {
  Form,
  Button,
} from 'antd';
import { Switch } from 'antd';
import { Checkbox } from 'antd';
import {  Input } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../../redux/actions';
import swal from 'sweetalert';  
import { List } from 'antd';
const columnsDirecciones = [
{
    title: 'Codigó Predio',
    dataIndex: 'codigoPredio',
    key: 'codigoPredio',
},
{
    title: 'Dirección',
    dataIndex: 'direccion',
    key: 'direccion',
},
{
    title: 'Numero',
    dataIndex: 'numero',
    key: 'numero',
}
];


  const  GestionPermisos=(props)=> {
    const [form] = Form.useForm();
    const [componentSize, setComponentSize] = useState('large');
    const [selectedRowKeys, setSelectedRowKeys] = useState([]);
    const [selectedRowKeysResultDeuda, setSelectedRowKeysResultDeuda] = useState([]);
    const listRef = React.createRef();
    const [totalResultado, setTotalResultado] = useState(0.0);
    const [realizarPagoCuotas, setRealizarPagoCuotas] = useState(false);
    const [totalResultadoSeleccionado, setTotalResultadoSeleccionado] = useState(0.0);
    const [totalResultadoSeleccionadoPagar, setTotalResultadoSeleccionadoPagar] = useState(0.0);
    const [listadoDirecciones, setListadoDirecciones] = React.useState([]);
    const [listadoResultado, setListadoResultado] = React.useState([]);
    const columnsDeudaPredial = [
      {
          title: 'Año',
          dataIndex: 'anio',
          key: 'anio',
      },
      {
        title: 'Pago Fraccionado',
        dataIndex: 'pagoFraccionado',
        key: 'pagoFraccionado',
        render: (value, row, index) => { return <div>
          <Switch disabled={ !(row.ultimoAnio && row.id==null)} checked={row.pagoFraccionado}  onChange={(e)=>{cambiarEstadoFraccionado(index)}} /></div> }  // just an example
    },
      {
          title: 'Codigó Predio',
          dataIndex: 'codigoPredio',
          key: 'codigoPredio',
      },
      {
          title: 'Dirección',
          dataIndex: 'direccion',
          key: 'direccion',
      },
      {
          title: 'Numero',
          dataIndex: 'numero',
          key: 'numero',
      },
      {
          title: 'Total',
          dataIndex: 'valorTotal',
          key: 'valorTotal',
      }
    ];
    const onSelectChange = selectedRowKeys => {
      setSelectedRowKeys(selectedRowKeys);
      setTotalResultadoSeleccionado(0.0);
      setSelectedRowKeysResultDeuda([]);
      cargarDeudasPredios(selectedRowKeys);
      
    };
    const onSelectChangeResultDeuda = selectedRowKeys => {
      setSelectedRowKeysResultDeuda(selectedRowKeys);
      if(selectedRowKeys.length==1){
        let tempMostrar=false;
        let codigoPredio=null;
        listadoResultado.forEach(record => {
          selectedRowKeys.forEach(elementSelect => {
            if((record.codigoPredio+"-"+record.anio)==elementSelect){
              if(record.ultimoAnio){
                tempMostrar=true;
                codigoPredio=record.codigoPredio;
              }
            }
            
          });
          
        });
        setRealizarPagoCuotas(false);
      }else{
        setRealizarPagoCuotas(false);
      }

    
    };
    const rowSelection = {
      selectedRowKeys,
      onChange: onSelectChange,
    };
    const rowSelectionResultDeuda = {
      selectedRowKeys:selectedRowKeysResultDeuda,
      onChange: onSelectChangeResultDeuda,
      getCheckboxProps: (record) => ({
        disabled: record.id!=null ? true : false
      })
      //realizoPago
    };
   
  
    const dispatch = useDispatch();
    const onFormLayoutChange = ({ size }) => {
      setComponentSize(size);
    };
    useEffect(()=>{
        let isSubscribed = true
        if(listRef?.current!=null){
          if (!isSubscribed) return;
        }
        
        return () => isSubscribed = false;
    },[]);
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        console.log("listadoResultado",listadoResultado);
        //suma de los primer nivel
        let sumaAcomulador=0.0;

        listadoResultado.forEach(element => {
          if(element.id==null){
            //record.codigoPredio+"-"+record.anio
            if(!element.pagoFraccionado){
              let existVal=selectedRowKeysResultDeuda.filter((val)=>{
                return val==(element.codigoPredio+"-"+element.anio)
              });
              if(existVal.length>0){
                sumaAcomulador+=parseFloat(element.valorTotal);
              }
            }else{
              element.detalle.forEach(elementDet => {
                  if(!elementDet.realizopagodefault){
                    if(elementDet.realizopago){
                      sumaAcomulador+=parseFloat(elementDet.valorpago);
                    }
                  }
              });

            }


          }else{
            element.detalle.forEach(elementDet => {
              if(!elementDet.realizopagodefault){
                if(elementDet.realizopago){
                  sumaAcomulador+=parseFloat(elementDet.valorpago);
                }
              }
          });
          }
        });

        console.log("sumaAcomulador",sumaAcomulador);
        setTotalResultadoSeleccionadoPagar(sumaAcomulador);
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
  },[listadoResultado,selectedRowKeysResultDeuda]);
    /**listadoResultado */


    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        recalcularMontos();
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
    },[listadoResultado]);
      //  
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){

        recalcularMontos();
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
    },[selectedRowKeysResultDeuda]);
    const verificarDni = async(dni) => {
      if(dni.target.value=="" || dni.target.value==null || dni.target.value==undefined){

        form.setFieldsValue({
          predio:undefined
        });
        form.setFieldsValue({
          nombreCompletos:null
        });
        return false;
      }
      form.setFieldsValue({
        predioSeleccionado:undefined
      });
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `datosUsuarioCiudadano/buscarCiudadano?dni=`+dni.target.value,
          {},
          "get"
        );
        console.log("respon",respon);
        //setCiudadanoBuscado(respon.estado ? respon.data : null    );
        if(respon.estado){ //predio
          form.setFieldsValue({
              nombreCompletos:respon.data.nombres,
              apellidoPaterno:respon.data.apellidoPaterno,
              apellidoMaterno:respon.data.apellidoMaterno
          });
          setListadoDirecciones(respon.dataCiudadanosPredio);
        }else{
          form.setFieldsValue({
            nombreCompletos:null
          });
        }
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
      
    };
    const recalcularMontos = async() => {
      let sumTotal=0.0;
      listadoResultado.forEach(record => {
        let itemSubTotal=0.0;
        selectedRowKeysResultDeuda.forEach(element => {
          if((record.codigoPredio+"-"+record.anio)==element ){
            itemSubTotal=parseFloat(parseFloat(record.valorTotal).toFixed(2));
            sumTotal+=itemSubTotal;
          }
        });

      });
      
      setTotalResultadoSeleccionado(sumTotal);
      
    };
    const registrarPago=()=>{
      /*swal({
        title: "Actualizar Usuario.",
        text: respon.mensaje,
        icon:  "success",
        button: "aceptar"
      });*/

      swal({
        title: "Impuesto Predial",
        text: " ¿Deseas Registrar Pago?",
        icon: "warning",
      // buttons: true,
        dangerMode: true,
        buttons: {
          cancel: "Cancelar",
          catch: {
            text: "Aceptar",
            value: "catch",
          }
        }
      })
      .then(async (willDelete) => {
        if (willDelete) {
          let newArrayImpuestoPredial=[];
          //record.codigoPredio+"-"+record.anio

          listadoResultado.forEach(record => {
            let arrayExist=selectedRowKeysResultDeuda.filter(elementDeuda => {
              return (record.codigoPredio+"-"+record.anio)==elementDeuda;
            });
            if(arrayExist.length){
              let newListSubItem=[];
              record.detalle.forEach(element => {
                if(element.realizopago){
                  newListSubItem.push({
                    "valorpago":element.valorpago,
                    "mespago":element.mespago,
                    "id":element.id
                  });
                }
                
              });
              newArrayImpuestoPredial.push({
                "pagofraccionado":newListSubItem.length ? 1 : 0,
                "valorpago":record.valorTotal,
                "idpredio":record.idPredio,
                "id":record.id,
                "aniopago":record.anio,
                "detallepagosimpuestopredial":newListSubItem,
              });
            }
            
          });
          let request={};
          request["pagosImpuestoPredial"] =newArrayImpuestoPredial;
          let respon=await requestApi(
            `impuestos/registrarPagoImpuestoPredialv2`,
            request,
            "post"
          );

          if(respon.estado){
            swal({
              title: "Registro de Impuesto Predial",
              text: respon.mensaje,
              icon:  respon.estado ? "success":"error",
              button: "aceptar"
            });
            await resetearFormulario();
          }else{
            swal({
              title: "Registro de Impuesto Predial",
              text: respon.mensaje,
              icon:  respon.estado ? "success":"error",
              button: "aceptar"
            });
            //setErrorMensaje(respon.mensaje);
          }
        }
      });

    } 
    const resetearFormulario=()=>{
      form.setFieldsValue({
        "dni":null,
        "nombreCompletos":null,
        "apellidoPaterno":null,
        "apellidoMaterno":null
      });
      setSelectedRowKeys([]);
      setSelectedRowKeysResultDeuda([]);
      //setSelectedRowKeysResultDeuda([]);
      setRealizarPagoCuotas(false);
      setListadoDirecciones([]);
      setListadoResultado([]);
      
    }
    const cargarDeudasPredios = async(selectedRowKeysV) => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `impuestos/listarDeudaPredial?numeroPredios=`+selectedRowKeysV.join(),
          {},
          "get"
        );
        console.log("respon",respon);
        if(respon.estado){ //predio
          setListadoResultado(respon.data);
          setSelectedRowKeysResultDeuda(respon.dataFormatSelect);
          //valorTotal
          let datosList=respon.data;
          let sumTotal=0.0;
          datosList.forEach(element => {
            sumTotal+=parseFloat(parseFloat(element.valorTotal).toFixed(2) );
          });
          setTotalResultado(sumTotal);
        }else{
          setListadoResultado([]);
          setSelectedRowKeysResultDeuda([]);
          
        }

        
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
      
    };  
    const seleccionarCheckboxlistadoResultado=(index,indexSub)=>{

      let listTemp=JSON.parse(JSON.stringify(listadoResultado));
      listTemp[index].detalle[indexSub].realizopago=!listTemp[index].detalle[indexSub].realizopago;
      let almenosunoSeleccionado=listTemp[index].detalle.filter(element => {
        return element.realizopago==true;
      });
      if(almenosunoSeleccionado.length>0){
        let existsss=selectedRowKeysResultDeuda.filter((val)=>{
          return val==listTemp[index].codigoPredio+"-"+listTemp[index].anio
        });
        if(existsss.length==0){
          setSelectedRowKeysResultDeuda(selectedRowKeysResultDeuda => [...selectedRowKeysResultDeuda, (listTemp[index].codigoPredio+"-"+listTemp[index].anio)]);
          //
        }
      }else{
        let newList=selectedRowKeysResultDeuda.filter((val)=>{
          return val!=listTemp[index].codigoPredio+"-"+listTemp[index].anio
        });
        setSelectedRowKeysResultDeuda(newList);

      }
      setListadoResultado(listTemp);
    }
    const cambiarEstadoFraccionado=(index,target)=>{
      let listTemp=JSON.parse(JSON.stringify(listadoResultado));
      listTemp[index].pagoFraccionado=!listTemp[index].pagoFraccionado;
      //realizopago;
      listTemp[index].detalle.forEach(element => {
        element.realizopago=false;
      });
      setListadoResultado(listTemp);
    }
    return (
      <div ref={listRef}   className="contentPago">
        <Card title={"Gestionar Impuesto"} bordered={false} style={{ width: "100%" }}>
          <>
          <Form
              labelCol={{
              span: 4,
              }}
              wrapperCol={{
              span: 14,
              }}
              layout="vertical"
              initialValues={{
              size: componentSize,
              }}
              onValuesChange={onFormLayoutChange}
              size={componentSize}
              form={form}
          >
              <Layout>
                  <Row>
                      <Col lg={12} >
                          <Form.Item
                              label="Dni"
                              name="dni"
                              rules={[{ required: true, message: 'Se requiere de dni.' }]}
                              wrapperCol={{ lg: 22 }} 
                              
                          >
                              <Input onBlur={(e)=>{verificarDni(e);}} />
                          </Form.Item>                
                      </Col>
                      <Col lg={12}>
                          <Form.Item
                              label="Nombres Completos"
                              name="nombreCompletos"
                              rules={[{ required: true, message: 'Se requiere de dni.' }]}
                              wrapperCol={{ lg: 22 }} 
                              labelCol={{ lg: 24 }}
                              
                          >
                              <Input  disabled/>
                          </Form.Item>                
                      </Col>
                      <Col lg={12}>
                          <Form.Item
                              label="Apellido Paterno"
                              name="apellidoPaterno"
                              rules={[{ required: true, message: 'Se requiere de dni.' }]}
                              wrapperCol={{ lg: 22 }} 
                              labelCol={{ lg: 24 }}
                              
                          >
                              <Input  disabled/>
                          </Form.Item>                
                      </Col>
                      <Col lg={12}>
                          <Form.Item
                              label="Apellido Materno"
                              name="apellidoMaterno"
                              rules={[{ required: true, message: 'Se requiere de dni.' }]}
                              wrapperCol={{ lg: 22 }} 
                              labelCol={{ lg: 24 }}
                              
                          >
                              <Input disabled/>
                          </Form.Item>                
                      </Col>
                  </Row>
              </Layout>
          </Form>
          <div>
         
              <Table 
                rowSelection={rowSelection} 
                dataSource={listadoDirecciones} 
                columns={columnsDirecciones}
                rowKey={(record) =>(record.codigoPredio)}
              />
            <Table 
                rowSelection={rowSelectionResultDeuda} 
                dataSource={listadoResultado} 
                columns={columnsDeudaPredial}
                rowKey={(record) =>(record.codigoPredio+"-"+record.anio)}
                expandable={{
                  expandedRowRender: (record,index) =>  <List
                  header={null}
                  footer={null}
                  bordered
                  dataSource={record.detalle==null ? [] : record.detalle}
                  renderItem={(item,indexPrincipal) => (
                    <List.Item style={{display: "flex"}}>
                      
                      <Checkbox style={{display: "flex"}} disabled={item.realizopagodefault}   checked={item.realizopago} onChange={(e)=>{seleccionarCheckboxlistadoResultado(index,indexPrincipal)}}>
                        <div style={{display:"flex"}}>
                          <p style={{minWidth:180}}>{record.pagoFraccionado==false  ? item.descripcion : item.descripcion}</p>{item.valorpago}
                        </div>
                        
                        </Checkbox> 
                    </List.Item>
                  )}
                />,
                  rowExpandable: record => record.pagoFraccionado,
                }}
              />
            <div className="row">
                <span><strong>Total Seleccionado Pagar:</strong>S./{totalResultadoSeleccionadoPagar}</span>
            </div>
            <div className="row">
                <span><strong>Total:</strong> S./{totalResultado}</span>
            </div>
            <div className="row">
                <span><strong>Total Pagado:</strong>S./{totalResultadoSeleccionado}</span>
            </div>

          </div>
          
          </>
          <Button style={{marginTop:20}} type="primary" onClick={()=>{registrarPago()}}>Registrar Pago</Button>

        </Card>
      </div>
      );
  
  }
export default GestionPermisos;