import {useState,useEffect} from 'react';

import { Layout } from 'antd';
import  {requestApi}  from '../../api/index';
import {  useDispatch } from "react-redux";
import { Button,Table } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {loaderSpiner} from '../../redux/actions';
//import GestionImpuestoComponent from './GestionTipoDeImpuesto';
import FormularioTipoDeImpuesto from './FormularioTipoDeImpuesto';
const {  Content } = Layout;
const  GestionarTipoDeImpuesto=(props)=> {
  
    const columns = [
      { title: 'Descripciòn', dataIndex: 'descripcion', key: 'descripcion',center:true },
      { title: 'Vigencia', dataIndex: 'vigencia', key: 'vigencia' },
      { title: 'Fecha registro', dataIndex: 'fechaRegistro', key: 'fechaRegistro' },
      {
        title: 'Editar',
        dataIndex: 'editar',
        sorter: false,
        render: (data,data2) => <Button type="primary" onClick={()=>{editar(data2)}}>Editar</Button>
        ,
        width: '20%'
      },      
    ];
    const [gestionarNuevo, setGestionarNuevo] = useState(1);
    const [idEdit, setIdEdit] = useState(1);
    const [anioSeleccionado, setAnioSeleccionado] = useState(null);
    const [listadoTipoImpuesto, setListadoTipoImpuesto] = useState([]);
    const listRef = React.createRef();
    const dispatch = useDispatch();
    useEffect(()=>{
      let isSubscribed = true;
      if(listRef?.current!=null){
        listarTipoImpuesto();
        return () => isSubscribed = false;
      }
      
      return () => isSubscribed = false;
    },[]);
    useEffect(()=>{
      let isSubscribed = true;
      return () => isSubscribed = false;
  },[]);
    const listarTipoImpuesto = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `impuestos/listadoTipoImpuesto`,
          {},
          "get"
        );
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
        setListadoTipoImpuesto(respon.estado ? respon.data : []);
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }
    
    };

    const nuevoImpuestos = () => {
      setGestionarNuevo(2);
    };
 
    const editar=async(item)=>{
      setIdEdit(item.id);
      setGestionarNuevo(3);
      setAnioSeleccionado(item);
    }

    return (
      <div ref={listRef}   className="contentPago">
        {
          gestionarNuevo==1 ?
            <Card title={"Gestionar Tipo de Impuesto"} bordered={false} style={{ width: "100%" }}>
              
              <Button   type="primary" onClick={nuevoImpuestos}>
                Agregar 
              </Button><br/>
             
              <Content>

                  <Table
                    pagination={false}
                    columns={columns}
                    rowKey={record => record.id}
                    
                    expandable={{
                      expandedRowRender: record => <p style={{ margin: 0 }}>{record.description}</p>,
                      rowExpandable: record => true,
                    }}
                
                    dataSource={listadoTipoImpuesto}
                  />

              </Content>
              </Card> : null
        }
        {
          gestionarNuevo==2 ?
          <FormularioTipoDeImpuesto atras={()=>{setGestionarNuevo(1);listarTipoImpuesto();}}/>: null
        }
        {
          gestionarNuevo==3 ?
          <FormularioTipoDeImpuesto atras={()=>{setGestionarNuevo(1);listarTipoImpuesto();}} idEdit={idEdit}/>: null
        }
      </div>
      );
 
}
export default React.memo(GestionarTipoDeImpuesto) ;