import {useState,useEffect} from 'react';
import  {requestApi}  from '../../api/index';
import swal from 'sweetalert';
import { PageHeader } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
import { Layout } from 'antd';
import { Row, Col } from 'antd';
import {
    Form,
    Input,
    Button,
    Switch,Table,Modal
  } from 'antd';
import { Checkbox } from 'antd';
import { Radio } from 'antd';
const { TextArea } = Input;
const {  Content } = Layout;
const  FormularioCiudadano=(props)=> {
    const columnsTipoEvento = [
      {
        title: 'Acción',
        dataIndex: 'key',
        sorter: false,
        render: (data,hola) => 
        <div style={{display:"flex"}}>
          <Button className="btnAccion" type="primary" onClick={()=>{editarDireccion(hola)}}>Editar</Button>
        </div>
        ,
        width: '40%',
        key: 'accion-01',
      },
      {
        title: 'Descripciòn',
        dataIndex: 'descripcion',
        sorter: false,
        render: data => `${data}`,
        width: '20%',
        key: 'dni-01',
      },
      {
        title: 'Porcentaje Impuesto',
        dataIndex: 'porcentajeImpuesto',
        sorter: false,
        render: data => `${data}`,
        width: '20%',
        key: 'dni-01',
      },
      {
        title: 'Varia % por Valor Max',
        dataIndex: 'variaPorcentajePorValorText',
        sorter: false,
        width: '20%',
        key: 'variaPorValorMax-01',
        render: data => `${data}`,
      },
      {
        title: 'Vigencia',
        dataIndex: 'vigenciaEventoText',
        sorter: false,
        width: '20%',
        key: 'vigencia',
        render: data => `${data}`
      }
    ];
    const columnsTableDetailt = [
      {
        title: 'Valor %Menor Igual en UIT',
        dataIndex: 'valorMenorCondicion',
        key: 'valorMenorCondicion',
      },
      {
        title: 'Nuevo Porcentaje Valor % Exepción',
        dataIndex: 'valorCondicion',
        key: 'valorCondicion',
      }
    ];
    const [form] = Form.useForm();
    const [formEvento] = Form.useForm();
    const [componentSize, setComponentSize] = useState('large');
    const listRef = React.createRef();
    const [vigencia, setVigencia] = React.useState(true);
    const [vigenciaEvento, setVigenciaEvento] = React.useState(true);
    const [variaPorcentajeValor, setVariaPorcentajeValor] = React.useState(false);
    const [loading,setLoading] = useState(false);
    const [isModalDirecciones, setIsModalDirecciones] = React.useState(false);
    const [listadoValoresExp, setListadoValoresExp] = React.useState([]);
    const [listadoEventos, setlistadoEventos] = React.useState([]);
    const [tipoActividad, setTipoActividad] = React.useState(2);
    const [actPermanente, setActPermanente] = React.useState(false);
    const [actTemporales, setActTemporales] = React.useState(false);
    const [idEditEvento, setIdEditEvento] = React.useState(null);
    const [idEditDato, setIdEditDato] = React.useState(null);
  
    const onChange = e => {
      console.log('radio checked', e.target.value);
      setTipoActividad(e.target.value);
      setlistadoEventos([]);
    };

    const dispatch = useDispatch();
    const onFormLayoutChange = ({ size }) => {
      setComponentSize(size);
    };
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if(props.idEdit!=undefined){
          verTipoImpuesto(props.idEdit);
        }
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
    },[props.idEdit]);
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if(!variaPorcentajeValor){
          let templistadoValoresExp=[...listadoValoresExp];
          templistadoValoresExp.forEach((element,index) => {
            //formEvento.setFieldsValue();
            let key01='valorMenor-'+index;
            let key02='valorExepcion-'+index;
            let valoress={};
            valoress[key01]="";
            valoress[key02]="";
            formEvento.setFieldsValue(valoress);
          });
          setListadoValoresExp([]);
        }
        if (!isSubscribed) return;

      }
      
      return () => isSubscribed = false;
    },[variaPorcentajeValor]);
    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
    },[]);
    useEffect(()=>{
        let isSubscribed = true
        if(listRef?.current!=null){
          //cargarGenero();
          if (!isSubscribed) return;
        }
        
        return () => isSubscribed = false;
    },[]);

    useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        //form.getFieldValue("tipoActividad")
        setTipoActividad(form.getFieldValue("tipoActividad"));
        if (!isSubscribed) return;
      }
      
      return () => isSubscribed = false;
  },[form.getFieldValue("tipoActividad")]);
    /*
    tipoActividad
    */
    const verTipoImpuesto=async(id)=>{
      try{
        let respon=await requestApi(
          `impuestos/getTipoImpuesto?id=`+id,
          {},
          "get"
        );
        let newItemVer=respon.estado ? respon.data[0] : {};
        form.setFieldsValue({
          descripcion:newItemVer.descripcion,
          porcentajeImpuesto:newItemVer.porcentajeImpuesto,
          tipoActividad:newItemVer.tipoActividadImpuesto,
          descripcionActPermanente:newItemVer.descripcionTiempoPermanenciaPermanente,
          descripcionActTemp:newItemVer.descripcionTiempoPermanenciaTemporales
        });
        setActPermanente(newItemVer.descripcionTiempoPermanenciaPermanente==null ? false : true);
        setActTemporales(newItemVer.descripcionTiempoPermanenciaTemporales==null ? false : true);
        
        //descripcionTiempoPermanenciaTemporales
        setVigencia(newItemVer.vigencia=="1" ? true : false);
        setlistadoEventos(newItemVer.listadoValoresExp);
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
        //variaPorcentajeValor
        formEvento.setFieldsValue({
          variaPorcentajeValor:true
        });
        setVariaPorcentajeValor(true);
        
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }

    }
    const registrarTipoImpuesto=async()=>{
      form.validateFields()
      .then(async(values) => {
        dispatch(loaderSpiner({
          loaderSpiner:true
        }));
        let newlistadoEventos=[];
        listadoEventos.forEach(element => {
          let newExp=[];
          element.listadoValoresExp.forEach(elementExp => {
            newExp.push({
              valorMaximo:elementExp.valorCondicion,
              porcentajeImpuesto:elementExp.valorMenorCondicion
            });
          });
          newlistadoEventos.push({
            "id":element.id==undefined ? null : element.id,
            "descripcion":element.descripcion,
            "porcentajeImpuesto":element.porcentajeImpuesto,
            "vigencia":element.vigenciaEvento,
            "tieneExepcionMontoMenores":element.variaPorcentajePorValor,
            //"tipoeventoexepcion":element.listadoValoresExp,
            "tipoeventoexepcion":newExp
          });
        });
        let dataRequest={
          "id":props.idEdit==undefined  ? null : props.idEdit,
          "descripcion":form.getFieldValue("descripcion"),
          "porcentajeImpuesto":form.getFieldValue("porcentajeImpuesto")==undefined ? null : form.getFieldValue("porcentajeImpuesto"),
          "vigencia":vigencia,
          "tipoActividadImpuesto":form.getFieldValue("tipoActividad"),
          "descripcionTiempoPermanenciaTemporal":form.getFieldValue("descripcionActTemp")==undefined ? "" : form.getFieldValue("descripcionActTemp"),
          "descripcionTiempoPermanenciaPermanente":form.getFieldValue("descripcionActPermanente")==undefined ? "" : form.getFieldValue("descripcionActPermanente"),
          "tipoEvento":listadoEventos
        };
        try{
          let respon=await requestApi(
            `impuestos/registrarTipoPermanencia`,
            dataRequest,
            "post"
          );
          props.atras();
          console.log("respon",respon);
          if(respon.estado){
            swal({
              title: "Actualizar Permisos.",
              text: respon.mensaje,
              icon:  respon.estado ? "success":"error",
              button: "aceptar"
            });
            
          }else{
            swal({
              title: "Actualizar Permisos.",
              text: respon.mensaje,
              icon:  respon.estado ? "success":"error",
              button: "aceptar"
            });
          }
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));

        }catch(e){
          return [];
        }finally{
          dispatch(loaderSpiner({
            loaderSpiner:false
          }));      
        }

        //console.log("dataRequest:",dataRequest);
      })
      .catch((errorInfo) => {
        
      });

    
    }
    const editarDireccion=(request)=>{
      setIdEditDato(request.id);
      setIdEditEvento(request.key);
      formEvento.setFieldsValue({
        descripcion:request.descripcion,
        porcentajeImpuesto:request.porcentajeImpuesto,
        descripcion:request.descripcion
      });
      setVariaPorcentajeValor(request.variaPorcentajePorValor);
      setVigenciaEvento(request.vigencia=="1" ? true : false);
      let newList=[];
      for (let index = 0; index <  request.listadoValoresExp.length; index++) {
        let key01='valorMenor-'+index;
        let key02='valorExepcion-'+index;
        let valoress={};
        valoress[key01]=request.listadoValoresExp[index].valorMenorCondicion;
        valoress[key02]=request.listadoValoresExp[index].valorCondicion;
        valoress.valorCondicion=request.listadoValoresExp[index].valorCondicion;
        valoress.valorMenorCondicion=request.listadoValoresExp[index].valorMenorCondicion;
        newList.push(valoress);
        formEvento.setFieldsValue(valoress);
      }
      setListadoValoresExp(newList);
      setIsModalDirecciones(true);

    }
    const handleEventos= async()=> {
     
      formEvento.validateFields().then((error)=>{
        if(variaPorcentajeValor){
          if(listadoValoresExp.length==0){
            swal({
              title: "EVENTOS EXISTENTES",
              text: "Se requiere de al menos de un item en Variación Exepción.",
              icon:  "warning",
              button: "aceptar"
            });
            return false;
          }
        }
        //idEditEvento==null || idEditEvento==""  || idEditEvento==undefined ?  listadoEventos.length : idEditEvento
        console.log("idEditEvento",idEditEvento);
        if(!(idEditEvento===null || idEditEvento===""  || idEditEvento===undefined ) ){
          console.log("if()");
          let templistadoEventos=JSON.parse(JSON.stringify(listadoEventos));
          templistadoEventos.forEach(element => {
            if(element.key==idEditEvento){
              element.id=idEditDato;
              element.descripcion=formEvento.getFieldValue("descripcion");
              element.porcentajeImpuesto=formEvento.getFieldValue("porcentajeImpuesto");
              element.variaPorcentajePorValor=variaPorcentajeValor;
              element.variaPorcentajePorValorText=variaPorcentajeValor ? "SI" :  "NO";
              element.vigenciaEventoText=vigenciaEvento ? "ACTIVO" :  "INACTIVO";
              element.vigenciaEvento=vigenciaEvento;
              element.listadoValoresExp=JSON.parse(JSON.stringify(listadoValoresExp));

            }
          });
          let templistadoValoresExp=JSON.parse(JSON.stringify(listadoValoresExp));
          templistadoValoresExp.forEach((element,index) => {
            //formEvento.setFieldsValue();
            let key01='valorMenor-'+index;
            let key02='valorExepcion-'+index;
            let valoress={};
            valoress[key01]="";
            valoress[key02]="";
            formEvento.setFieldsValue(valoress);
          });

          setListadoValoresExp([]);
          setlistadoEventos(templistadoEventos);
          setIsModalDirecciones(false);
        }else{
          console.log("else()");
          setlistadoEventos(listadoEventos => [...listadoEventos, {
            descripcion:formEvento.getFieldValue("descripcion"),
            porcentajeImpuesto:formEvento.getFieldValue("porcentajeImpuesto"),
            variaPorcentajePorValor:variaPorcentajeValor,
            variaPorcentajePorValorText:variaPorcentajeValor ? "SI" :  "NO",
            vigenciaEventoText:vigenciaEvento ? "ACTIVO" :  "INACTIVO",
            vigenciaEvento:vigenciaEvento,
            listadoValoresExp:JSON.parse(JSON.stringify(listadoValoresExp)),
            key:idEditEvento==null || idEditEvento==""  || idEditEvento==undefined ?  listadoEventos.length : idEditEvento
          }]);
          setIsModalDirecciones(false);
          let templistadoValoresExp=[...listadoValoresExp];

          templistadoValoresExp.forEach((element,index) => {
            //formEvento.setFieldsValue();
            let key01='valorMenor-'+index;
            let key02='valorExepcion-'+index;
            let valoress={};
            valoress[key01]="";
            valoress[key02]="";
            formEvento.setFieldsValue(valoress);
          });
          setListadoValoresExp([]);

        }

        //setlistadoEventos([]);
        //variaPorcentajeValor
      });

    }
    const handleOnCancel= ()=> {
      setIsModalDirecciones(false);
    }
    const nuevoEvento=()=>{
      setIsModalDirecciones(true);
      formEvento.resetFields();
      setListadoValoresExp([]);
      setIdEditEvento(null);
      setIdEditDato(null);
      //idEditDato
    }
    const anadirExepciones= ()=> {
      setListadoValoresExp(listadoValoresExp => [...listadoValoresExp, {}]);
      //setlistadoEventos([]);
    }
    const asignarListValores= (i,e)=> {
    let templistadoValoresExp=[...listadoValoresExp];
    templistadoValoresExp[i][e.target.name+"-"+i]=e.target.value;
    //i
    templistadoValoresExp[i][e.target.name]=e.target.value;
    setListadoValoresExp(templistadoValoresExp);
    }
    const removeListValores= (i)=> {
      let templistadoValoresExpt=[...listadoValoresExp];
      templistadoValoresExpt=templistadoValoresExpt.filter((e,index)=>{return index!=i});
      setListadoValoresExp(templistadoValoresExpt);
    }
    return (
    <div ref={listRef}   className="contentPago">
      <Modal 
          maskClosable={false}
          width={800} 
          centered 
          title={<h3 className="titleModal">EVENTOS EXISTENTES</h3>}
          visible={isModalDirecciones} 
          onOk={handleEventos}
          onCancel={handleOnCancel}
          >
      <Form
        labelCol={{
          span: 4,
          }}
          wrapperCol={{
          span: 12,
          }}
          layout="vertical"
          initialValues={{
          size: componentSize,
          }}
          size={componentSize}
          form={formEvento}
          className="formEvento"
          >
              <Form.Item 
                name={['descripcion']}
                label="Descripción"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Descripción.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                
              >
                <Input
                  name={['Inputdescripcion']}
                  type="text" 
                  autoComplete="off"
                />
                
              </Form.Item>
              <Form.Item 
                name={['porcentajeImpuesto']}
                label="Porcentaje Impuesto"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Porcentaje Impuesto.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                
              >
                <Input
                  name={['InputPorcentajeImpuesto']}
                  type="text" 
                  autoComplete="off"
                />
                
              </Form.Item>
              <Form.Item 
                name={[ 'variaPorcentajeValor']} 
                label="Varia % por Valor Max"
                wrapperCol={{ lg: 24,md:24,sm: 24 }}
              >
                  <Switch name={['variaPorcentajeValor']} checked={variaPorcentajeValor} onChange={(e)=>{setVariaPorcentajeValor(e)}} />
              </Form.Item>
              
              {
                variaPorcentajeValor ?
                <>
                  <Content  > 
                    {listadoValoresExp.map((item, i) => (
                        (
                          <Row key={i} > 
                            <Col lg={12}  >
                              <Form.Item 
                                  name={['valorMenor-'+i]}
                                  label="Valor %Menor Igual en UIT"
                                  max="500"
                                  rules={[{ required: true, message: 'Se Requiere de Valor %Menor Igual en UIT.' }]}
                                  wrapperCol={{col: 24, lg: 24,md:24,sm: 24 }}
                                  labelCol={{col: 24, lg: 24,md:24,sm: 24 }}
                                >
                                  <Input autoComplete="off" value={item.valorMenorCondicion} name="valorMenorCondicion" onInput={(e)=>{ asignarListValores(i,e);}} placeholder="" />
                              </Form.Item>
                            </Col>
                            <Col lg={10}  >
                            <Form.Item 
                                name={['valorExepcion-'+i]}
                                label="Nuevo Porcentaje Valor % Exepción"
                                max="500"
                                rules={[{ required: true, message: 'Se Requiere de Valor %Menor Igual en UIT.' }]}
                                wrapperCol={{ col: 24,lg: 24,md:24,sm: 24 }}
                                labelCol={{col: 24, lg: 24,md:24,sm: 24 }}
                                style={{marginLeft:20}}
                              >
                              <Input  autoComplete="off" value={item.valorCondicion} name="valorCondicion" onInput={(e)=>{ asignarListValores(i,e);}} placeholder="" />
                            </Form.Item>  
                            </Col>
                            <Col lg={2}  >
                              <span style={{position:"relative",left:10,top:40,cursor:"pointer",fontSize:25}} onClick={(e)=>{ removeListValores(i);}}>X</span>
                            </Col>
                        </Row  >  )

                        )  
                    )}
                </Content>
                <Button style={{marginLeft:10}} type="primary" onClick={(e)=>{anadirExepciones(e)}} >+</Button>
              </>
               : <></>

              }
             
              <Form.Item 
                name={[ 'vigenciaDir']} 
                label="Vigencia"
                wrapperCol={{ lg: 12,md:14,sm: 24 }}
              >
                  <Switch checked={vigenciaEvento} onChange={(e)=>{setVigenciaEvento(e)}} />
                  <span> { vigenciaEvento ? 'ACTIVO' : 'INACTIVO' }</span>
              </Form.Item>
          </Form>
      </Modal> 
      <Card 
          title={
          <PageHeader
            className="site-page-header"
            onBack={() => props.atras()}
            title={props.idEdit ? "Editar Tipo de Impuesto" : "Nuevo Tipo de Impuesto" }
            subTitle={null}
          />} bordered={false} style={{ width: "100%" }}>
          <>
          <Form
              labelCol={{
              span: 24,
              }}
              wrapperCol={{
              span: 24,
              }}
              layout="vertical"
              initialValues={{
              size: componentSize,
              tipoActividad:2
              }}
              onValuesChange={onFormLayoutChange}
              size={componentSize}
              form={form}
          >
 
              <Form.Item 
                name={[ 'descripcion']}
                label="Descripción"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Descripción.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                >
                  <Input  autoComplete="off" />
              </Form.Item>
              <Form.Item 
                name={[ 'tipoActividad']}
                label=""
                rules={[{ required: false }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                >
                <Radio.Group onChange={onChange}>
                  <Radio value={2}>NO ES TIPO ACTIVIDAD</Radio>
                  <Radio value={1}>ES TIPO ACTIVIDAD</Radio>
                 
                </Radio.Group>
              </Form.Item>
              {
                tipoActividad==1 ?
                <div>
                  <Checkbox checked={actPermanente} value={actPermanente}  onChange={(e)=>{setActPermanente(e.target.checked);form.setFieldsValue({
                    descripcionActPermanente:""
                  });}} >permanentes</Checkbox>
                  {
                    actPermanente ? 
                      <Form.Item 
                        name={[ 'descripcionActPermanente']}
                        label="Descripción de Tiempo Permanencia"
                        max="500"
                        rules={[{ required: true, message: 'Se Requiere de Descripción Tiempo de Permanencia.' }]}
                        wrapperCol={{ lg: 12,md:24,sm: 24 }}
                        >
                          <TextArea rows={4}   />
                      </Form.Item> : 
                    null
                  }
 

                  <Checkbox checked={actTemporales} value={actTemporales}  onChange={(e)=>{setActTemporales(e.target.checked);
                  form.setFieldsValue({
                    descripcionActTemp:""
                  });}} >Temporales o Eventuales</Checkbox>
                  {
                    actTemporales ? 
                    <Form.Item 
                      name={[ 'descripcionActTemp']}
                      label="Descripción de Tiempo Permanencia"
                      max="500"
                      rules={[{ required: true, message: 'Se Requiere de Descripción Tiempo de Permanencia.' }]}
                      wrapperCol={{ lg: 12,md:24,sm: 24 }}
                      >
                        <TextArea rows={4}  />

                        
                    </Form.Item> : 
                    null
                  }


              </div> : 
                null
              }

              {
                tipoActividad==2 ?

                <Form.Item 
                name={[ 'descripcionActTemp']}
                label="Descripción de Tiempo Permanencia"
                max="500"
                rules={[{ required: true, message: 'Se Requiere de Descripción Tiempo de Permanencia.' }]}
                wrapperCol={{ lg: 12,md:24,sm: 24 }}
                >
                  <TextArea rows={4}   />
              </Form.Item> : 

              null
              }



                {
                tipoActividad==2 ?
                  <Form.Item 
                  name={[ 'porcentajeImpuesto']}
                  label="Porcentaje Impuesto"
                  max="500"
                  rules={[{ required: true, message: 'Se Requiere de Descripción Tiempo de Permanencia.' }]}
                  wrapperCol={{ lg: 12,md:24,sm: 24 }}
                  >
                   <Input
                  name={['InputPorcentajeImpuesto']}
                  type="text" 
                  autoComplete="off"
                />
                </Form.Item>:
                null
              }              
              <Form.Item 
                name={[ 'vigencia']} 
                label="Vigencia"
                wrapperCol={{ lg: 12,md:14,sm: 24 }}
              >
                  <Switch checked={vigencia} onChange={()=>{setVigencia(!vigencia);}} />
              </Form.Item>
              {
                tipoActividad==1 ? 

                <Button  className="btnNuevoRegistrar" type="primary" onClick={()=>{ nuevoEvento()}}>Tipos eventos</Button>
                : null
              }
              
              {
                tipoActividad==1 ?
                <div>
                  <Table
                  className="contentTable"
                  scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
                  columns={columnsTipoEvento}
                  dataSource={listadoEventos}
                  rowKey={(record) =>(record.key)}
                  pagination={false}
                  loading={loading}
                  expandable={{
                    expandedRowRender: record => 
                    <div style={{ margin: 0 }}>
                      <Table dataSource={record.listadoValoresExp} columns={columnsTableDetailt} />
                    </div>,
                    rowExpandable: record => record.listadoValoresExp.length ? true : false,
                  }}
                />
                </div>
                  
                : 
                  null


              }

              <Button style={{marginTop: 97}}  htmlType="submit" className="btnNuevoRegistrar" type="primary" onClick={()=>{ registrarTipoImpuesto()}}>Guardar</Button>
          </Form>
          </>
        
        </Card>
    </div>
    );
 
}
export default FormularioCiudadano;