import {useState,useEffect} from 'react';
import { Layout } from 'antd';

import moment from 'moment';
import swal from 'sweetalert';
import  {requestApi}  from '../../api/index';
import { List } from 'antd';
import {  useDispatch } from "react-redux";
import { Button,Table } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {loaderSpiner} from '../../redux/actions';
import GestionImpuestoComponent from './GestionImpuesto';
import VerGestionImpuestoComponent from './VerGestionImpuesto';
const {  Content } = Layout;
const  ListadoGestionImpuesto=(props)=> {
  
    const columns = [
      { title: 'Año', dataIndex: 'anio', key: 'anio',center:true },
      { title: 'Sub Total', dataIndex: 'subTotal', key: 'subTotal' },
      { title: 'Vigente', dataIndex: 'vigenciaText', key: 'vigenciaText' },
      {
        title: 'ver',
        dataIndex: 'anio',
        sorter: false,
        render: data => <Button type="primary" onClick={()=>{verImpuesto(data)}}>ver</Button>
        ,
        width: '20%'
      },
    ];
    const [gestionarNuevo, setGestionarNuevo] = useState(1);
    const [anioSeleccionado, setAnioSeleccionado] = useState(null);
    const [listadoImpuestoAnuales, setListadoImpuestoAnuales] = useState([]);
    const listRef = React.createRef();
    const dispatch = useDispatch();
    useEffect(()=>{
      let isSubscribed = true;
      if(listRef?.current!=null){
        listarImpuestoAnuales();
        return () => isSubscribed = false;
      }
      
      return () => isSubscribed = false;
    },[]);
    useEffect(()=>{
      let isSubscribed = true;
      return () => isSubscribed = false;
  },[]);
    const listarImpuestoAnuales = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `impuestos/listarImpuestosAnuales`,
          {},
          "get"
        );
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
        setListadoImpuestoAnuales(respon.estado ? respon.data : []);
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }
    
    };

    const nuevoImpuestos = () => {
      setGestionarNuevo(2);
    };
 
    const verImpuesto=async(item)=>{
      setGestionarNuevo(3);
      console.log("item",item);
      setAnioSeleccionado(item);
    }

    return (
      <div ref={listRef}   className="contentPago">
        {
          gestionarNuevo==1 ?
            <Card title={"Gestionar Impuesto Anual"} bordered={false} style={{ width: "100%" }}>
              <Button type="primary" onClick={nuevoImpuestos}>
                Agregar o Actualizar
              </Button><br/>
              <Content>

                  <Table
                    columns={columns}
                    rowKey={record => record.anio}
                    expandable={{
                      expandedRowRender: record => <div style={{ margin: 0 }}>{
                        ( <List
                          header={null}
                          footer={null}
                          bordered
                          dataSource={record.listadoDefiniciones}
                          renderItem={(item,indexList) => (
                          null
                          )}
                        />)
                      }</div>,
                      rowExpandable: record => false,
                    }}
                    dataSource={listadoImpuestoAnuales}
                  />

              </Content>
              </Card> : null
        }
        {
          gestionarNuevo==2 ?
          <GestionImpuestoComponent atras={()=>{setGestionarNuevo(1);listarImpuestoAnuales();}}/>: null
        }
        {
          gestionarNuevo==3 ?
          <VerGestionImpuestoComponent anioSeleccionado={anioSeleccionado} atras={()=>{setGestionarNuevo(1);listarImpuestoAnuales();}}/>: null
        }
      </div>
      );
 
}
export default ListadoGestionImpuesto;