import {useState,useEffect} from 'react';
import { Layout } from 'antd';
import  {requestApi}  from '../../api/index';
import { List } from 'antd';
import {  useDispatch } from "react-redux";
import { Button,Table } from 'antd';
import { Card } from 'antd';
import * as React from 'react';
import {loaderSpiner} from '../../redux/actions';
import GestionImpuestoComponent from './GestionImpuesto';
import VerGestionImpuestoComponent from './VerGestionImpuesto';
const {  Content } = Layout;
const  GestionarTipoDeImpuesto=(props)=> {
  
    const columns = [
      { title: 'Descripciòn', dataIndex: 'descripcion', key: 'descripcion',center:true },
      { title: 'Vigencia', dataIndex: 'vigencia', key: 'vigencia' },
      { title: 'Fecha registro', dataIndex: 'fechaRegistro', key: 'fechaRegistro' }
    ];
    const [gestionarNuevo, setGestionarNuevo] = useState(1);
    const [anioSeleccionado, setAnioSeleccionado] = useState(null);
    const [listadoTipoPermanencia, setListadoTipoPermanencia] = useState([]);
    const listRef = React.createRef();
    const dispatch = useDispatch();
    useEffect(()=>{
      let isSubscribed = true;
      if(listRef?.current!=null){
        listarTipoImpuesto();
        return () => isSubscribed = false;
      }
      
      return () => isSubscribed = false;
    },[]);
    useEffect(()=>{
      let isSubscribed = true;
      return () => isSubscribed = false;
  },[]);
    const listarTipoImpuesto = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `impuestos/listadoPermanencia`,
          {},
          "get"
        );
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
        setListadoTipoPermanencia(respon.estado ? respon.data : []);
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }
    
    };

    const nuevoImpuestos = () => {
      setGestionarNuevo(2);
    };
 
    const verImpuesto=async(item)=>{
      setGestionarNuevo(3);
      setAnioSeleccionado(item);
    }

    return (
      <div ref={listRef}   className="contentPago">
        {
          gestionarNuevo==1 ?
            <Card title={"Gestionar Tipo de Permanencia"} bordered={false} style={{ width: "100%" }}>
              
              <Button style={{ display:'none'}}  type="primary" onClick={nuevoImpuestos}>
                Agregar
              </Button><br/>
             
              <Content>

                  <Table
                    pagination={false}
                    columns={columns}
                    rowKey={record => record.anio}
                    expandable={{
                      expandedRowRender: record => <div style={{ margin: 0 }}>{
                        ( <List
                          header={null}
                          footer={null}
                          bordered
                          dataSource={record.listadoDefiniciones}
                          renderItem={(item,indexList) => (
                          null
                          )}
                        />)
                      }</div>,
                      rowExpandable: record => false,
                    }}
                    dataSource={listadoTipoPermanencia}
                  />

              </Content>
              </Card> : null
        }
        {
          gestionarNuevo==2 ?
          <GestionImpuestoComponent atras={()=>{setGestionarNuevo(1);listarTipoImpuesto();}}/>: null
        }
        {
          gestionarNuevo==3 ?
          <VerGestionImpuestoComponent anioSeleccionado={anioSeleccionado} atras={()=>{setGestionarNuevo(1);listarTipoImpuesto();}}/>: null
        }
      </div>
      );
 
}
export default GestionarTipoDeImpuesto;