import * as React from 'react';
import {useState,useEffect} from 'react';
import { Table } from 'antd';
import { Select } from 'antd';
//import reqwest from 'reqwest';
import { Card } from 'antd';
//import { Form } from 'antd';
import {  Button } from 'antd';
//import { Layout } from 'antd';
import { Pagination } from 'antd';
import { Menu } from 'antd';
//import swal from 'sweetalert';
import {  useDispatch } from "react-redux";
import {loaderSpiner} from '../../redux/actions';
//import { DatePicker  } from 'antd';
import  {requestApi}  from '../../api/index';
import FormularioUsuarioComponent from './FormularioUsuario';
import FormularioEditarUsuarioComponent from './FormularioEditarUsuario';
import PermisosUsuarioComponent from './PermisosUsuario';
const { Option } = Select;



const  GestionUsuarios=()=> {
  const menu = (
    <Menu>
      <Menu.Item key="0">
        <a href="https://www.antgroup.com">Excel</a>
      </Menu.Item>
      <Menu.Item key="1">
        <a href="https://www.aliyun.com">Pdf</a>
      </Menu.Item>
    </Menu>
  );
  const columns = [
    {
      title: 'Acción',
      dataIndex: 'id',
      sorter: false,
      render: data => <Button type="primary" onClick={()=>{darPermiso(data)}}>Editar</Button>
      ,
      width: '20%'
    },
    {
      title: 'Dni',
      dataIndex: 'dni',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Correo',
      dataIndex: 'correo',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Nombres',
      dataIndex: 'nombres',
      sorter: false,
      width: '20%',
      render: data => `${data}`,
    },
    {
      title: 'Apelido Paterno',
      dataIndex: 'apellidosPaterno',
      sorter: false,
      width: '20%',
      render: data => `${data}`
    },
    {
      title: 'Apelido Materno',
      dataIndex: 'apellidoMaterno',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Genero',
      dataIndex: 'generoDescripcion',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Cargo',
      dataIndex: 'cargoDescripcion',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
    {
      title: 'Vigencia',
      dataIndex: 'vigenciaDescripcion',
      sorter: false,
      render: data => `${data}`,
      width: '20%'
    },
  ];
  const [loading,setLoading] = useState(false);
  const dispatch = useDispatch();
  const [listadoDataUsuario,setListadoDataUsuario] = useState([]);
  const listRef = React.createRef();
  const [tipoVista, setTipoVista] = useState(1);
  const [contarTotal, setContarTotal] = useState(0);
  const [paginaActual, setPaginaActual] = useState(1);
  const [cantidadContenido, setCantidadContenido] = useState(10);
  const [usuarioSeleccionado, setUsuarioSeleccionado] = useState(null);
  //const [data,setData] = useState([]);
  useEffect(()=>{
      let isSubscribed = true
      if(listRef?.current!=null){
        if (!isSubscribed) return;
        (async()=>{
          await contarUsuario();
          await listarUsuario(paginaActual-1,cantidadContenido);
        })();
      }
      
      return () => isSubscribed = false;
  },[]);
  const darPermiso=async(reqId)=>{
    
    //console.log("reqId",reqId);
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `usuarioAdmin/verUsuarioAdmin?id=`+reqId,
        {},
        "get"
      );
      setUsuarioSeleccionado(respon.estado ? respon.data[0] :null);
      setTipoVista(3);
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
  }
  const limpiarFiltro=()=>{
 
  }
  const refrescarListado=()=>{
    listarUsuario(paginaActual-1,cantidadContenido);
  }
  const listarUsuario = async(reqpaginaActual,reqcantidadContenido) => {
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `usuarioAdmin/listarUsuarioAdmin?paginaactual=${reqpaginaActual}&cantidadcontenido=${reqcantidadContenido}`,
        {},
        "get"
      );
      setListadoDataUsuario(respon.estado ? respon.data : []  );
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
   
  };
  const contarUsuario = async() => {
    dispatch(loaderSpiner({
      loaderSpiner:true
    }));
    try{
      let respon=await requestApi(
        `usuarioAdmin/contarUsuarioAdmin`,
        {},
        "get"
      );
      setContarTotal(respon.estado ? respon.contar : 0  );
    }catch(e){
      return e;
    }finally{
      dispatch(loaderSpiner({
        loaderSpiner:false
      }));
    }
   
  };
  const changePagination=async(val,sizePagination)=>{
    setPaginaActual(val);
    setCantidadContenido(sizePagination);
    await contarUsuario();
    await listarUsuario(val-1,sizePagination);
 }
  const nuevo=(e)=>{
    setTipoVista(2);
  }
  return (
    <div ref={listRef}   className="contentPago">
      {
        tipoVista==1 ?
          <div>
            <Card title="Gestión de Usuarios" bordered={false} style={{ width: "100%" }}>
                <div className="contentBtnNuevo" style={{position:"relative"}}>
                  <Button type="primary"  className="btnNuevo" onClick={nuevo} >Nuevo</Button>
                 </div>
                
            </Card>
          
            <Table
              className="tableGestionUsuario"
              scroll={{ x: 'calc(700px + 50%)',  y: 1500 }}
              columns={columns}
              dataSource={listadoDataUsuario}
              pagination={false}
              rowKey={record => record.correo}
              loading={loading}
            />
           <Pagination  className="paginationTable" current={paginaActual} defaultCurrent={paginaActual} total={contarTotal}  onChange={(val,size)=>{changePagination(val,size)}} />
                     
            </div> :
            null
      }

      {
        tipoVista==2 ? <FormularioUsuarioComponent atras={()=>{setTipoVista(1)}}/> : null
      }
      {
        tipoVista==3 ? <FormularioEditarUsuarioComponent dataEdit={usuarioSeleccionado} atras={()=>{setTipoVista(1)}} refrescarListado={()=>{refrescarListado()}}/> : null
      }
    </div>
    );
 
}
export default GestionUsuarios;