import {useState,useEffect} from 'react';
import { PageHeader } from 'antd';
import { Layout } from 'antd';
import { Row, Col,Switch } from 'antd';
import { DatePicker  } from 'antd';
import moment from 'moment';
import  {requestApi}  from '../../api/index';
import swal from 'sweetalert';
import { List} from 'antd';
import {  useDispatch } from "react-redux";
import { Input } from 'antd';
import { Modal } from 'antd';
import { Checkbox } from 'antd';
import { Table } from 'antd';

import {
  Button
} from 'antd';

import { Card } from 'antd';
import * as React from 'react';
import {loaderSpiner} from '../../redux/actions';
import { Transfer } from 'antd';
const {  Content } = Layout;
const  GestionPermisos=(props)=> {
  
    const columns = [
      { title: 'Fecha Inicio', dataIndex: 'fechaInicioText', key: 'fechaInicio' },
      { title: 'Fecha Vencimiento', dataIndex: 'fechaVencimientoText', key: 'fechaVencimiento' },
      { title: 'Sub Total', dataIndex: 'subTotal', key: 'subTotal' },
      { title: 'Vigente', dataIndex: 'vigenciaText', key: 'vigenciaText' },
      {
        title: 'Editar',
        dataIndex: 'key',
        sorter: false,
        render: data => <Button type="primary" onClick={()=>{editarMontos(data)}}>Editar</Button>
        ,
        width: '20%'
      },
    ];
    const [listadoDefiniciones, setListadoDefiniciones] = useState([]);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [fechaInicio, setFechaInicio] = useState(null);
    const [anioSeleccionado, setAnioSeleccionado] = useState(null);
    const [fechaVencimiento, setFechaVencimiento] = useState(null);
    const [vigencia, setVigencia] = useState(true);
    const [listadoDetalleDefinicionesNuevo, setListadoDetalleDefinicionesNuevo] = useState([]);
    const listRef = React.createRef();
    const dispatch = useDispatch();
    useEffect(()=>{
      console.log("moment().endOf('year')-yes:",moment("01-01-2021").endOf('year'));
      let isSubscribed = true;
      if(listRef?.current!=null){
        cargarDefiniciones();
        //if (!isSubscribed) return;
        return () => isSubscribed = false;
      }
      
      return () => isSubscribed = false;
    },[]);
    useEffect(()=>{
      let isSubscribed = true;
      return () => isSubscribed = false;
  },[]);
    const cargarDefiniciones = async() => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `impuestos/cargarImpuestos`,
          {},
          "get"
        );
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
        setListadoDefiniciones(respon.estado ? respon.data : []);
        //respon.estado ? respon.data : []
      }catch(e){
        return [];
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));      
      }
    
    };

    const actualizarCostos=async (index,valText)=> {
      let tempListadoDefiniciones=JSON.parse(JSON.stringify(listadoDefiniciones));
      tempListadoDefiniciones.forEach((element,indexFor) => {
        if(indexFor==index){
          console.log("valText.target.value",valText.target.value);
          element["valor"]=element["pagar"]===true ?   (parseFloat(valText.target.value) ) : null;
          //mensajeErrorCosto
          if(element["valor"]!=null){
            if(element["valor"]<0){
              element["mensajeErrorCosto"]="El monto no debe ser negativo.";
            }else{
              element["mensajeErrorCosto"]=null;
            }
          }else{
            element["mensajeErrorCosto"]=null;
          }
        }
      });
      setListadoDefiniciones(JSON.parse(JSON.stringify(tempListadoDefiniciones)));
    }
    const showModal = () => {
      resetearModal();
      if(anioSeleccionado==null){
        swal({
          title: "Gestion Impuestos.",
          text:"Se requiere de Seleccion de Año.",
          icon:"error",
          button: "aceptar"
        });
        return false;
      }
      setIsModalVisible(true);
    };
    const handleOk = () => {
      let itemNuevo={};
      if(fechaInicio=="" || fechaInicio==null){
        swal({
          title: "Gestion Impuestos.",
          text:"Se requiere de fecha Inicio.",
          icon:"error",
          button: "aceptar"
        });
        return false;
      }
      if(fechaVencimiento=="" || fechaVencimiento==null){
        swal({
          title: "Gestion Impuestos.",
          text:"Se requiere de fecha Vencimiento.",
          icon:"error",
          button: "aceptar"
        });
        return false;
      }
      if(fechaInicio>fechaVencimiento){
        swal({
          title: "Gestion Impuestos.",
          text:"La fecha de Inicio debe ser mayor a la fecha de vencimiento.",
          icon:"error",
          button: "aceptar"
        });
        return false;
      }
      itemNuevo["fechaInicioText"]=moment(fechaInicio).format("DD-MM-YYYY");
      itemNuevo["fechaInicio"]=fechaInicio;
      itemNuevo["fechaVencimiento"]=fechaVencimiento;
      itemNuevo["fechaVencimientoText"]=moment(fechaVencimiento).format("DD-MM-YYYY");
      itemNuevo["vigencia"]=vigencia;
      itemNuevo["vigenciaText"]=vigencia ? "ACTIVO" : "INACTIVO";
      itemNuevo["listadoDefiniciones"]=JSON.parse(JSON.stringify(listadoDefiniciones));
      let subTotal=0.0;//subTotal
      listadoDefiniciones.forEach(element => {
        subTotal+=(element["valor"]==null || element["valor"]=="" ? 0.0 : parseFloat(element["valor"]));
      });
      itemNuevo["subTotal"]=parseFloat(subTotal).toFixed(2);
      let listNuevolistadoDetalleDefinicionesNuevo=JSON.parse(JSON.stringify(listadoDetalleDefinicionesNuevo));
      let existeItem=listadoDetalleDefinicionesNuevo.filter((val)=>{
        return (val.fechaInicio==fechaInicio && val.fechaVencimiento==fechaVencimiento);
      });

      let existeItemHabilitado=listadoDefiniciones.filter((val)=>{
        return val["pagar"]===true ;
      });
      let existeItemVacio=listadoDefiniciones.filter((val)=>{
        return val["pagar"]===true && (val["valor"]==="" || val["valor"]===null) ;
      });
      if(existeItemVacio.length>0){
        swal({
          title: "Gestion Impuestos.",
          text:"Se requiere Valor Cuando este habilitado el Pago.",
          icon:"error",
          button: "aceptar"
        });
        return false;
      }


      if(existeItemHabilitado.length==0 ){
        swal({
          title: "Gestionar Impuesto",
          text: "Se requiere Al menos un valor Detalle Impuesto.",
          icon: "warning",
          button: "aceptar"
        });
        return false;
      }
      let existeVal=listadoDefiniciones.filter((val)=>{
        return val["mensajeErrorCosto"]=="" || val["mensajeErrorCosto"]==null || val["mensajeErrorCosto"]==undefined ? false : true;
      });
      if(existeVal.length>0 ){
        swal({
          title: "Gestionar Impuesto",
          text: "Los montos  no deben ser negativos.",
          icon: "warning",
          button: "aceptar"
        });
        return false;
      }
      if(existeItem.length>0){
        let copialistNuevolistadoDetalleDefinicionesNuevo=JSON.parse(JSON.stringify(listNuevolistadoDetalleDefinicionesNuevo));
        copialistNuevolistadoDetalleDefinicionesNuevo.forEach((val)=>{
          if(val.fechaInicioText==moment(fechaInicio).format("DD-MM-YYYY") && val.fechaVencimientoText==moment(fechaVencimiento).format("DD-MM-YYYY")){
            val["fechaInicioText"]=moment(fechaInicio).format("DD-MM-YYYY");
            val["fechaInicio"]=fechaInicio;
            val["fechaVencimiento"]=fechaVencimiento;
            val["fechaVencimientoText"]=moment(fechaVencimiento).format("DD-MM-YYYY");
            val["vigencia"]=vigencia;
            val["vigenciaText"]=vigencia ? "ACTIVO" : "INACTIVO";
            val["subTotal"]=parseFloat(subTotal).toFixed(2);
            val["listadoDefiniciones"]=JSON.parse(JSON.stringify(listadoDefiniciones));
        
          }
        });
        setListadoDetalleDefinicionesNuevo(copialistNuevolistadoDetalleDefinicionesNuevo);
      }else{
        itemNuevo["key"]=listNuevolistadoDetalleDefinicionesNuevo.length;
        listNuevolistadoDetalleDefinicionesNuevo.push(itemNuevo);
        setListadoDetalleDefinicionesNuevo(JSON.parse(JSON.stringify(listNuevolistadoDetalleDefinicionesNuevo)));
      }
     
      setIsModalVisible(false);
      //resetearModal();
    };
    const editarMontos=async(item)=>{
      await cargarDefiniciones();
      let itemEdit=listadoDetalleDefinicionesNuevo[item];
      setVigencia(itemEdit["vigencia"]);
      setFechaInicio(itemEdit["fechaInicio"]); 
      setFechaVencimiento(itemEdit["fechaVencimiento"]);
      setListadoDefiniciones(itemEdit["listadoDefiniciones"]);
      setIsModalVisible(true);

    }
    const handleCancel = () => {
      resetearModal();
      setIsModalVisible(false);
    };
    const onChangeCheck=(index,e)=> {
      let tempListadoDefiniciones=[...listadoDefiniciones];
     
      tempListadoDefiniciones.forEach((element,indexFor) => {
        if(indexFor==index){
          element["pagar"]=e.target.checked;
          if(!e.target.checked){
            element["valor"]=null;
            element["mensajeErrorCosto"]=null;

          }
        }
      });
      setListadoDefiniciones(tempListadoDefiniciones);
    }
    const registrarImpuesto=async()=>{
      if(listadoDetalleDefinicionesNuevo.length==0){
        swal({
          title: "Gestionar Impuesto",
          text: "Se requiere Al menos un Detalle.",
          icon: "warning",
          button: "aceptar"
        });
      }
      swal({
        title: "Gestion de Impuestos",
        text: " ¿Deseas Guardar Cambios?",
        icon: "warning",
       // buttons: true,
        dangerMode: true,
        buttons: {
          cancel: "Cancelar",
          catch: {
            text: "Aceptar",
            value: "catch",
          }
        }
      })
      .then(async(willDelete) => {
        if (willDelete) {
          dispatch(loaderSpiner({
            loaderSpiner:true
          }));
          try{
            let dataRequest={
              "periodosImpuesto":listadoDetalleDefinicionesNuevo,
            };
            let respon=await requestApi(
              `impuestos/registrarImpuestos`,
              dataRequest,
              "post"
            );
            if(respon.estado){
              swal({
                title: "Registrar Impuesto.",
                text: respon.mensaje,
                icon:  respon.estado ? "success":"error",
                button: "aceptar"
              });
              props.atras();
            }else{
              
              swal({
                title: "Registrar Impuesto.",
                text: respon.mensaje,
                icon:  respon.estado ? "success":"error",
                button: "aceptar"
              });
            }
          }catch(e){
            return e;
          }finally{
            dispatch(loaderSpiner({
              loaderSpiner:false
            }));
          }
        }
      });
      
    }
    const resetearModal=()=>{
      setVigencia(true);
      setFechaInicio(null);
      setFechaVencimiento(null);
      let tempListadoDefiniciones=JSON.parse(JSON.stringify(listadoDefiniciones));
      tempListadoDefiniciones.forEach((element,indexFor) => {
        element["pagar"]=false;
        element["valor"]=null;
      });
      setListadoDefiniciones(tempListadoDefiniciones);
    }
    const disabledDate= (current)=> {
      return current && ( current < moment(anioSeleccionado).add(-1, 'y').endOf('year') || current > moment(anioSeleccionado).endOf('year') );
    }
    const disabledDateYeah= (current)=> {
      return current && ( current < moment().endOf('year').add(-1, 'y')  );
    }
    const verImpuestoAnual = async(anio) => {
      dispatch(loaderSpiner({
        loaderSpiner:true
      }));
      try{
        let respon=await requestApi(
          `impuestos/verImpuestosAnuales?anio=`+anio,
          {},
          "get"
        );
        //setListadoDefiniciones
        if(respon.estado){
          setListadoDetalleDefinicionesNuevo(respon.data);
        }else{
          setListadoDetalleDefinicionesNuevo([]);
        }
      }catch(e){
        return e;
      }finally{
        dispatch(loaderSpiner({
          loaderSpiner:false
        }));
      }
     
    };
  const cambiarAnio=(value)=>{
    if(value!=null){
      setAnioSeleccionado(value._d);//
      verImpuestoAnual(moment(value._d).format("YYYY") );//moment(value._d, 'YYYY-MM-DD')
    }else{
      setAnioSeleccionado(null);
      setListadoDetalleDefinicionesNuevo([]);
    }
  }  
  return (
      <div ref={listRef}   className="contentPago">
        <Card title={<PageHeader
        className="site-page-header-left"
        onBack={() =>{props.atras()}}
        title="Gestionar Impuesto Anual"
        subTitle={null}
      />} style={{ width: "100%",float:"left" }}  bordered={false} >
        <div >
          <label>Año</label><br/>
          <DatePicker 
            className="DatePickeryear"  
            picker="year"
            disabledDate={disabledDateYeah}  
            value={anioSeleccionado==null ? null :  moment(anioSeleccionado)} 
            onChange={(value)=>{
              cambiarAnio(value);
             
            
            } }
          />
        </div><br/>
        <Button type="primary" onClick={showModal}>
          Agregar Nuevo
        </Button><br/>
        <Content>

            <Table
              columns={columns}
              expandable={{
                expandedRowRender: record => <div style={{ margin: 0 }}>{
                  ( <List
                    header={null}
                    footer={null}
                    bordered
                    dataSource={record.listadoDefiniciones}
                    renderItem={(item,indexList) => (
                     <>
                      <List.Item key={"prop-listadoDefiniciones-a-"+indexList}>
                       <Content>
                          <Row>
                            <Col xs={{ span: 12 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 12 }} >{item.descripcion}</Col>
                            <Col xs={{ span: 12 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 5 }} >{item.valor==null || item.valor=="" ?  "" : ("S./"+parseFloat(item.valor).toFixed(2)) }</Col>
                          </Row>
                        </Content>
                      </List.Item>
                      {
                        (record.listadoDefiniciones.length-1)==indexList ?
                        (<List.Item key={indexList}>
                          <Content>
                             <Row>
                               <Col xs={{ span: 12 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 12 }} >{'SUB TOTAL'}</Col>
                               <Col xs={{ span: 12 }} sm={{ span: 12 }} md={{ span: 12 }} lg={{ span: 5 }} >{record.subTotal }</Col>
                             </Row>
                           </Content>
                         </List.Item>) : null

                        
                       }
                     </>


                    )}
                  />)
                }</div>,
                rowExpandable: record => true,
              }}
              dataSource={listadoDetalleDefinicionesNuevo}
            />

        </Content>
        <Modal wrapClassName="modalPermiso" 
            okText="Registrar" 
            cancelText="Cancelar" 
            centered 
            title="Gestionar Impuesto-Detalle" 
            width={"100%"} 
            visible={isModalVisible}
            onOk={handleOk} 
            onCancel={handleCancel}>
              <div
              >
                <Row>
                  <Col xs={12} sm={12} md={12} lg={12} xl={12}>
                    <div>
                      <label>Fecha Inicio</label>
                      <DatePicker 
                        className="datapinckerfull" 
                        placeholder="Fecha Inicio"
                       
                        disabledDate={disabledDate}  
                        value={fechaInicio==null ? null :  moment(fechaInicio, 'YYYY-MM-DD')} 
                        onChange={(value)=>{
                          if(value!=null){
                            setFechaInicio(value._d);
                          }else{
                            setFechaInicio(null);
                          }
                        
                        } }  
                      />
                    </div> 
                  </Col>
                  <Col xs={12} sm={12} md={12} lg={24} xl={12}>
                    <div>
                        <label>Fecha Vencimiento</label>
                         <DatePicker 
                          className="datapinckerfull" 
                          value={fechaVencimiento==null ? null :  moment(fechaVencimiento, 'YYYY-MM-DD')}   
                          placeholder="Fecha Vencimiento"
                          disabledDate={disabledDate}  
                          onChange={(value)=>{
                            if(value!=null){
                              setFechaVencimiento(value._d);
                            }else{
                              setFechaVencimiento(null);
                            }
                          } }  />
                    </div>
                   
                  </Col>
                  <Col xs={12} sm={12} md={12} lg={12} xl={12}>
                    <div>
                      <label>Vigencia</label><br/>
                      <Switch checked={vigencia}  value={vigencia} onChange={(e)=>{setVigencia(e );}} />
                    </div>
                    <br/>
                  </Col>
                </Row>
              </div>
                
              
                
             
              <List
                header={<div><strong> Detalle Impuesto Definiciones </strong></div>}
                footer={null}
                bordered
                dataSource={listadoDefiniciones}
                renderItem={(item,index) => (
                  <List.Item key={"listadoDefiniciones-"+item.idDefinicionImpuesto}>
                  <Content>
                    <Row>
                      <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                      {item.descripcion}
                      </Col>
                      <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                        <Checkbox defaultChecked={item["pagar"]===true ? true : false} checked={item["pagar"]===true ? true : false}  onChange={(e)=>{onChangeCheck(index,e)}} >Habilitar Pago</Checkbox>
                        {
                          item["pagar"]===true ? 
                          <>
                          <Input type="number" placeholder="Costos" value={item["valor"]} min={0} onChange={(val)=>{actualizarCostos(index,val)}} />
                          <span className="errorMensaje">{item["mensajeErrorCosto"]==null || item["mensajeErrorCosto"]=="" || item["mensajeErrorCosto"]==undefined ? "" : item["mensajeErrorCosto"] }</span>
                          </> :
                          null
                        }
                      
                      </Col>
                    </Row>
                  </Content>
                  </List.Item>
            )}
          />
              

        </Modal>
            <Button type="primary" onClick={registrarImpuesto}>
              Registrar
            </Button>
        </Card>
      </div>
      );
 
}
export default GestionPermisos;