import React, { useEffect }  from 'react';
import { useHistory } from "react-router-dom";
import { BrowserRouter, Switch } from 'react-router-dom';
import { Route} from 'react-router';
import ListadoPagosComponent from '../components/ListadoPagos';

import ComponentNotFound from '../components/NotFound';
import SincronizacionDatosComponent from '../components/SincronizacionDatos';
import GestionUsuariosComponent from '../components/gestionUsuarios/GestionUsuarios';
import GestionPermisosComponent from '../components/gestionPermisos/GestionPermisos';
import ListadoGestionImpuestoComponent from '../components/Impuesto/ListadoGestionImpuesto';
import GestionCiudadanosComponent from '../components/gestionCiudadanos/GestionCiudadanos';
import GestionListadoGestionImpuestoCiudadano from '../components/Impuesto/gestionImpuestoCiudadano/ListadoGestionImpuestoCiudadano';
import GestionCargoComponent from '../components/gestionPermisos/gestionCargo/GestionCargo';
import ComponentContent from '../components/ConentGeneral';
import LoginComponent from '../components/login';
import GestionarTipoDeImpuesto from '../components/TipoImpuesto/GestionarTipoDeImpuesto';
import GestionTipoPermanencia from '../components/Impuesto/GestionTipoPermanencia';
import PrincipalImpuestoPredial from '../components/Impuesto/ImpuestoPredial/PrincipalImpuestoPredial';
const AppPrincipal=(props)=>{
  const history = useHistory();
  useEffect(()=>{
  },[history]);

  return (
    <BrowserRouter >
           
          <Switch>
          <Route
              path="/login"
              exact 
              render={() =><LoginComponent /> } />
            <Route
              path="/"
              exact 
              render={() =><ComponentContent> <ListadoPagosComponent /> </ComponentContent> } />
            <Route
              exact
              path="/pagos"
              render={() =><ComponentContent> <ListadoPagosComponent /> </ComponentContent> } />
            <Route
              exact 
              path="/sincronizacionDatos"
              render={() =><ComponentContent> <SincronizacionDatosComponent  /> </ComponentContent> } />
            <Route
              exact 
              path="/gestionUsuario"
              render={() =><ComponentContent> <GestionUsuariosComponent  /> </ComponentContent> } />
          
          <Route
              exact 
              path="/gestionPermisos"
              render={() =><ComponentContent> <GestionPermisosComponent /> </ComponentContent> } />
          
          <Route
              exact 
              path="/gestionImpuesto"
              render={() =><ComponentContent> <ListadoGestionImpuestoComponent /> </ComponentContent> } />
            <Route
              exact 
              path="/gestionCiudadanos"
              render={() =><ComponentContent> <GestionCiudadanosComponent  /> </ComponentContent> } />
            <Route
              exact 
              path="/gestionCargo"
              render={() =><ComponentContent> <GestionCargoComponent  /> </ComponentContent> } />  
            <Route
              exact 
              path="/gestionImpuestoCiudadanos"
              render={() =><ComponentContent> <GestionListadoGestionImpuestoCiudadano  /> </ComponentContent> } /> 
            <Route
              exact 
              path="/gestionTipoDeImpuesto"
              render={() =><ComponentContent> <GestionarTipoDeImpuesto  /> </ComponentContent> } /> 
            <Route
              exact 
              path="/gestionTipoPermanencia"
              render={() =><ComponentContent> <GestionTipoPermanencia  /> </ComponentContent> } /> 
            <Route
              exact 
              path="/gestionImpuestoAlcabala"
              render={() =><ComponentContent> <GestionTipoPermanencia  /> </ComponentContent> } />
            <Route
              exact 
              path="/gestionImpuestoPredial"
              render={() =><ComponentContent> <PrincipalImpuestoPredial  /> </ComponentContent> } />
            <Route
              exact 
              path="/gestionImpuestoJuego"
              render={() =><ComponentContent> <GestionTipoPermanencia  /> </ComponentContent> } />
            
            <Route
              exact 
              path="/gestionImpuestoEspectaculo"
              render={() =><ComponentContent> <GestionTipoPermanencia  /> </ComponentContent> } />

           <Route render={() =><> <ComponentNotFound  /> </> } />

          </Switch>
 
        </BrowserRouter>
      );
}
/*GestionarTipoDeImpuesto */
export default AppPrincipal;